.ranking-table-body{
  .heading-with-drop{
    .time{
      font-size: 12px;
      color: rgba($color: #000000, $alpha: 0.4);
    }
  }
}
.table-section{
  .table{
    margin-bottom: 0;
    .progress-bar{
      margin: 0 auto;
    }
    td,
    th{
      vertical-align: middle;
    }
    tr{
      th,
      td{
        padding-right: 6px;
        padding-left: 6px;
      }
      th:first-child,
      td:first-child{
        padding-left: 20px;
      }
      th:last-child,
      td:last-child{
        padding-right: 20px;
      }
    }
    .number{
      font-size: 16px;
      font-weight: 700;
    }
    .user-image{
      img{
        width: 32px;
        height: 32px;
        border-radius: 50%;
      }
    }
    .user-detail{
      width: 157.6px;
      .username{
        text-transform: capitalize;
        font-size: 14px;
        line-height: 1.2;
        color: rgba($color: $black, $alpha: 0.9);
        display: flex;
        align-items: center;
        .name{
          max-width: 102px;
          overflow-x: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        span + span{
          margin-left: 4px;
        }
        .position{
          margin-left: 4px;
          font-size: 12px;
          color: rgba(0, 0, 0, 0.4);
        }
      }
      .price{
        font-size: 15px;
        font-weight: 600;
        margin-top: 5px;
      }
    }
    .yday-percentage{
      p{
        display: flex;
        align-items: center;
        font-size: 12px;
        font-weight: 700;
        span{
          width: 12px;
          height: 12px;
        }
      }
    }
    .grey-bg{
      background-color: #F6F6F6;
      .progress-bar{
        div{
          span{
            background-color: #f6f6f6;
          }
        }
      }
    }
  }
}

//common table css
thead{
  th{
    font-size: 10px;
    color: rgba($color: #000000, $alpha: 0.6);
    text-transform: uppercase;
    border-bottom: 1px solid rgba($color: #000000, $alpha: 0.1) !important;
    border-top: none !important;
  }
}
//sticky header fix
#ranking{
  table{
    width: 100%;
    .target-heading{
      width: 157.6px;
    }
    & tbody {
      display:block;
      + tbody{
        max-height: 65vh;
        padding-bottom: 75px;
        overflow-y: auto;
        border-top: none !important;
      }
    }
    
    & thead, tbody tr {
      display:table;
      width:100%;
      table-layout: auto;
    }
    .number{
      width: 12%;
      min-width: 50px;
    }
    .user-image{
      width: 10%;
    }
    .user-detail,
    .target-heading{
      width: 40%;
    }
    .yday-percentage,
    .yday-heading{
      width: 18%;
    }
    .rank-heading{
      width: 22%;
    }
    .circular-chart,
    .chart-heading{
      width: 20%;
    }
    .target-heading,
    .yday-heading{
      padding-left: 18px
    }
  }
}