main{
  padding-top: 84px;
}
.top-section{
  padding-bottom: 55px;
}
.bottom-section{
  background: $white;
  box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  padding-top: 24px;
  position: relative;
}
#performance{
  .primary-card{
    .primary-card-footer{
      padding: 8px 12px 8px 16px;
    }
  }
  .top-section{
    .pinned{
      .kpi-empty{
        padding: 16px;
        background: $white;
        border-radius: 5px;
        flex-direction: row;
        justify-content: flex-start;
        position: relative;
        box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
        p{
          font-size: 14px;
          line-height: 22px;
          text-align: left;
          margin-left: inherit;
          margin-right: inherit;
        }
        img{
          width: 56px;
          height: 56px;
          margin-right: 16px;
        }
      }
      &.close-state{
        .close-button{
          position: absolute;
          top: 8px;
          right: 8px;
        }
        p{
          padding-right: 45px;
        }
      }
    }
  }
  .prp{
    .progress-body{
          margin-top: 16px;
    }
    .progress-card{
      padding: 16px 16px 28px;
    }
  }
  .bottom-section{
    section{
      scroll-margin-top: 128px;
    }
    .chips-navigation{
      padding-bottom: 10px;
      padding-top: 2px;
      margin-bottom: 22px;
    }
    .focus{
      .chips-radio{
        margin-bottom: 16px;
      }
      .flex-card-body{
        .primary-card-col{
            width: 100%;
            max-width: 50%;
        }
      }
      .kpi-empty{
        padding: 130px 0;
        img{
          width: 96px;
          height: 96px;
          margin-bottom: 32px;
        }
      }
    }
    .no-kpi{
      padding: 120px 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      img{
        width: 96px;
        height: 96px;
      }
      p{
        font-size: 18px;
        line-height: 22px;
        margin-top: 16px;
        text-align: center;
        color: $second-body-color;
      }
    }
  }
  .insight-card{
    padding: 16px;
    border-radius: 4px;
    background: $prp-light-blue;
    .insight-chips{
      display: inline-block;
      padding: 6px 9px;
      background: #3994d7;
      color: $white;
      text-transform: uppercase;
      border-radius: 4px;
      letter-spacing: 0.5px;
      font-size: 10px;
    }
    .insight-text{
      font-size: 14px;
      color: $prp-text;
      margin-top: 44px;
    }
  }
}
#primary-drill{
  .search-bar{
    input[type="text"]{
      font-size: 14px;
      padding: 12px 30px 12px 16px;
      }
    .close-icon{
      right: 16px;
    }
  }
  .multi-progress-bar{
    margin-top: 16px;
  }
  .my-team{
    .filter-bar{
      .chips-radio{
        overflow-x: scroll;
        margin-right: 0;
        .radio-label{
          width: auto;
          max-width: unset;
          white-space: nowrap;
        }
      }
    }
  }
  .filter-cards{
    .primary-card{
      max-width: 100%;
      +.primary-card{
        margin-top: 16px;
      }
      &.drill-2{
        .primary-card-body{
          padding-bottom: 32px;
        }
      }
      .primary-card-body{
        padding-bottom: 16px;
        .multi-progress-bar{
          margin-top: 0;
          padding-bottom: 22px;
        }
      }
      .primary-card-footer{
        display: flex;
        align-items: center;
        justify-content: space-between;
        p{
          display: flex;
          align-items: center;
          img{
            width: 24px;
            height: 24px;
            margin-left: 4px;
          }
          span{
            +span{
              margin-left: 4px;
            }
          }
        }
        .secondary-color{
          color: rgba($color: $white, $alpha: .8);
        }
        .trending-label{
          display: flex;
          align-items: center;
          font-size: 12px;
          font-weight: 400;
          .tail-arrow{
            width: 24px;
            height: 24px;
          }
        }
      }
    }
  }
}
#ta-accordion-l1{
  .top-section{
    padding-bottom: 32px;
  }
  .primary-card.red{
    max-width: 100%;
  }
  .chips-radio{
    overflow-x: scroll;
    margin-right: 0;
  }
}
.rssm{
  .seperate-card{
    width: 100%;
    max-width: 33.33%;
    min-width: 116px;
    box-sizing: content-box;
  }
}