//nav-tabs
.main-tabs{
  border-bottom: none;
  margin-bottom: 16px;
  li{
      +li{
          margin-left: 16px;
      }
      a{
          color: rgba($color: $black, $alpha: 0.4);
          font-size: 20px;
          font-weight: 500;
          line-height: 20px;
          display: flex;
          align-items: center;
          img{
              width: 32px;
              height: 32px;
              margin-left: 5px;
              filter: opacity(0.6);
          }
          &.active{
              color: $black;
              font-weight: 700;
              img{
                  filter: opacity(1);
              }
          }
      }
  }
}