.chart{
  .legend-body{
    display: flex;
    .legend{
      font-size: 12px;
      padding-left: 6px;
      position: relative;
      +.legend{
        margin-left: 32px;
      }
      &:nth-child(1){
        &::before{
          background-color: $legend-1;
        }
      }
      &:nth-child(2){
        &::before{
          background-color: $legend-2;
        }
      }
      &:nth-child(3){
        &::before{
          background-color: $legend-3;
        }
      }
      &::before{
        position: absolute;
        content: '';
        left: 0;
        top: 0;
        bottom: 0;
        width: 2px;
        border-radius: 10px;
      }
      .name{
        color: rgba(0,0,0, 0.6);
        font-weight: 400;
        max-width: 90px;
        word-break: break-all;
      }
      .value{
        font-weight: 700;
        display: flex;
        align-items: center;
        margin-top: 5px;
        span{
          margin-left: 4px;
          width: 12px;
          height: 12px;
        }
      }
    }
  }
}
.select-team{
  .instruction{
    font-size: 11px;
    line-height: 18px;
    font-style: italic;
    color: rgba(0,0,0, 0.6);
    margin-bottom: 24px;
  }
  .select-team-body{
    .select-team-header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 8px;
      .title{
        font-size: 10px;
        letter-spacing: 0.5px;
      }
      .search-button-icon{
        background: get-icon('search-icon', rgba(0,0,0,0.5));
      }
    }
    .main-select-team{
      position: relative;
      .main-select-team{
        padding-left: 24px;
        margin-top: 16px;
        &:after{
          position: absolute;
          content: '';
          left: 0;
          top: 0;
          bottom: 0;
          width: 1px;
          background: rgba(0,0,0, 0.1);
        }
        .filter-bar{
          margin-left: 0;
          .no-found{
            display: flex;
            align-items: center;
            img{
              width: 12px;
              height: 12px;
              margin-right: 10px;
            }
            .no-found-text{
              font-size: 14px;
              line-height: 18px;
              color: #2e2e2e;
            }
          }
          ul{
            li{
              &:first-child{
                margin-left: 0;
              }
            }
          }
        }
      }
    }
  }
  .filter-bar{
    overflow-x: auto;
    display: block;
    ul{
      position: relative;
      display: flex;
      &:after{
        content: ".";
        display: block;
        clear: both;
        visibility: hidden;
        line-height: 0;
        height: 0;
      }
      li{
        padding: 14px;
        white-space: nowrap;
        display: flex;
        align-items: center;
        border: 1px solid #e1e9ee;
        border-radius: 8px;
        margin-right: 8px;
        &.active{
          background-color: rgba($color: $blue, $alpha: 0.1);
          border-color: rgba($color: $blue, $alpha: 0.1);
          a{
            font-weight: 700;
            color: $blue;
          }
          .close-icon{
            display: block;
          }
        }
        &:first-child{
          margin-left: 20px;
        }
        a{
          font-size: 14px;
          color: $second-body-color;
          .filter-text{
            display: flex;
            align-items: center;
          }
          img{
            width: 24px;
            height: 24px;
            margin-right: 4px;
          }
          .tail-arrow{
            width: 12px;
            height: 12px;
            margin-left: 4px;
          }
        }
        .close-icon{
          width: 12px;
          height: 12px;
          margin-left: 12px;
          display: none;
        }
      }
    }
  }
}
#trends{
  .with-searchbar{
    display: none;
  }
  .search-body{
    display: flex;
    align-items: center;
  }
}