.progress-card{
  border-radius: 4px;
  padding: 16px 16px 54px;
  display: block;
  .progress-header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 8px;
      p{
          &:first-child{
              font-size: 20px;
              line-height: 20px;
              span{
                  font-size: 12px;
              }
          }
          &:last-child{
              font-size: 12px;
              line-height: 15px;
          }
      }
  }
  .progress-body{
      .custom-progress{
          display: flex;
          width: 100%;
          height: 4px;
          background-color: rgba($color: $white, $alpha: 0.7);
          border-radius: 2px;
          position: relative;
          .custom-progress-bar{
              border-radius: 2px;
              position: absolute;
              height: 4px;
              +.custom-progress-bar{
                  border-top-left-radius: 0;
                  border-bottom-left-radius: 0;
                  margin-left: -1px;
              }
              .custom-progress-label{
                  font-size: 12px;
                  line-height: 19px;
                  position: absolute;
                  top: 10px;
                  right: 0;
                  padding-top: 20px;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  &::after{
                      position: absolute;
                      content: '';
                      top: 0;
                      background: get-icon('triangle', $prp-text);
                      width: 12px;
                      height: 12px;
                      background-repeat: no-repeat;
                      background-size: contain;
                      background-position: center;
                  }
                  p{
                      position: absolute;
                      width: max-content;
                      margin-top: 8px;
                  }
              }
          }
      }
      .prp-progress{
          height: 11px;
          border: 1px dashed $red-card-text;
          border-radius: 4px;
          position: relative;
          .progress-reach{
              position: absolute;
              left: -1px;
              top: -1px;
              bottom: 0;
              background: $red-card-text;
              border: 1px solid $red-card-text;
              height: 11px;
              border-radius: 4px;
              &::after{
                position: absolute;
                content: '';
                top: calc(100% + 4px);
                right: -6px;
                background: get-icon('triangle', $red-card-text);
                width: 12px;
                height: 12px;
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
            }
          }
      }
  }
  &.prp-blue{
      background-color: $prp-light-blue;
      .primary-color{
          color: $prp-text;
      }
      .secondary-color{
          color: rgba($color: $prp-text, $alpha: .7);
      }
      .progress-body{
          border-color: $prp-text;
          .custom-progress{
              .custom-progress-bar{
                  &.primary-color{
                      background-color: $prp-text;
                  }
                  &.secondary-color{
                      background-color: rgba($color: $prp-text, $alpha: .2);
                  }
              }
          }
      }
  }
  &.prp-red{
        background-color: $red-card-bg;
        .primary-color{
            color: $red-card-text;
        }
        .secondary-color{
            color: rgba($color: $red-card-text, $alpha: .7);
        }
    }
}