.drill-main-card,
.drill-card-dashboard{
  min-width: 103px;
  max-width: 103px;
  padding: 12px;
  border-radius: 4px;
  margin-right: 8px;
  &:first-child{
    margin-left: 20px;
  }
  &.green{
    background-color: $green-card-bg;
    .primary-text{
      color: $green-card-text;
    }
    .secondary-text{
      color: rgba($color: $green-card-text, $alpha: .6);
    }
  }
  &.red{
    background-color: $red-card-bg;
    .primary-text{
      color: $red-card-text;
    }
    .secondary-text{
      color: rgba($color: $red-card-text, $alpha: .6);
    }
  }
  &.yellow{
    background-color: $yellow-card-bg;
    .primary-text{
      color: $yellow-card-text;
    }
    .secondary-text{
      color: rgba($color: $yellow-card-text, $alpha: .6);
    }
  }
  .primary-text{
    font-size: 20px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    span{
      margin-right: 4px;
    }
  }
  .secondary-text{
    font-size: 12px;
    line-height: 15px;
    margin-top: 8px;
    text-align: center;
  }
}
.unfocus{
  font-size: 12px;
  font-style: italic;
  color: rgba($color: $black, $alpha: 0.6);
  display: flex;
  align-items: center;
  margin-top: 52px;
  span{
    margin: 0 2px;
  }
}
.seperate-dashboard{
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow-x: auto;
  .seperate-card{
    padding: 16px;
    white-space: normal;
    min-width: 102px;
    height: 100%;
    position: relative;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    +.seperate-card{
      margin-left: 8px;
    }
    .primary-text,
    .seperate-card-count{
      font-size: 16px;
      display: flex;
      align-items: center;
      white-space: nowrap;
    }
    .secondary-text{
      font-size: 12px;
      margin-top: 4px;
    }
    &.green{
      background-color: $green-card-bg;
      .primary-text{
        color: $green-card-text;
      }
      .secondary-text,
      .secondary-color{
        color: rgba($color: $green-card-text, $alpha: .6);
      }
    }
    &.red{
      background-color: $red-card-bg;
      .primary-text{
        color: $red-card-text;
      }
      .secondary-text{
        color: rgba($color: $red-card-text, $alpha: .6);
      }
    }
    &.yellow{
      background-color: $yellow-card-bg;
      .primary-text{
        color: $yellow-card-text;
      }
      .secondary-text{
        color: rgba($color: $yellow-card-text, $alpha: .6);
      }
    }
  }
}
.drill-card-dashboard{
  min-width: initial;
  max-width: initial;
  padding: 16px;
  border-radius: 4px;
  margin-right: 0;
  display: flex;
  justify-content: space-between;
  .primary-text{
    font-size: 16px;
  }
  .secondary-text{
    margin-top: 4px;
  }
}
.drill-footer{
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #fff;
  padding: 8px 5px 0;
  margin-top: 32px;
  .drill-footer-text{
    font-size: 12px;
    line-height: 14px;
  }
  &.ta-footer{
    .big-text{
      font-size: 14px;
    }
    .right{
      display: flex;
      .drill-footer-text-count{
        +.drill-footer-text-count{
          margin-left: 24px;
        }
      }
    }
  }
}
.legend-dashboard{
  display: flex;
  .legend-list{
    position: relative;
    padding-left: 12px;
    +.legend-list{
      margin-left: 40px;
    }
    &.red{
      &:after{
        background: $red-card-footer;
      }
    }
    &.gray{
      &:after{
        background: #f3f3f3;
      }
    }
    &.yellow{
      &:after{
        background: $yellow-card-footer;
      }
    }
    &.green{
      &:after{
        background: $green-card-footer;
      }
    }
    &:after{
      content: '';
      position: absolute;
      width: 3px;
      border-radius: 10px;
      top: 0;
      bottom: 0;
      left: 0;
    }
    .legend-price{
      font-size: 16px;
      line-height: 20px;
    }
    .legend-name{
      font-size: 12px;
      line-height: 15px;
      color: rgba($color: $black, $alpha: 0.6);
      margin-top: 6px;
    }
  }
}

$rssm-circular-width: 72px;

.rssm-circular-charts{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 45px;
  margin-bottom: -24px;
  .circular-body{
    width: 33.33%;
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .circular-bottom-label{
      font-size: 12px;
      color: $black;
      margin-top: 8px;
    }
  }
  .progress-bar{
    width: $rssm-circular-width;
    height: $rssm-circular-width;
    .center-content{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    .price{
      font-size: 14px;
      font-weight: 700;
      color: $black;
      height: initial;
      width: initial;
      line-height: 18px;
    }
    .counter{
      font-size: 12px;
      color: rgba($color: $black, $alpha: 0.6);
      height: initial;
      width: initial;
      line-height: 18px;
    }
    div{
      width: $rssm-circular-width;
      height: $rssm-circular-width;
      span{
        line-height: $rssm-circular-width / 4 * 3;
        height: 69px;
        width: 69px;
      }
    }
    .rotate,
    .left,
    .right{
      clip: rect(0 ($rssm-circular-width / 2) $rssm-circular-width 0);
    }
  }
}
.shikhar{
  &.main-tabs{
    li{
      a{
        padding-bottom: 16px !important;
      }
    }
  }
  .seperate-dashboard{
    justify-content: start;
    .seperate-card{
      +.seperate-card{
        margin-left: 8px;
      }
    }
  }
  .onboard-bottom{
    margin-top: 32px;
    .on-board-title{
      font-size: 14px;
      font-weight: 400;
      margin-bottom: 16px;
      color: rgba($color: $black, $alpha: 0.9);
    }
  }
}