.bar-icon{
  width: 24px;
  height: 24px;
  background-image: get-icon('bar-icon');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.setting{
  .push-notification{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: $white;
    border-radius: 8px;
    padding: 20px 16px;
    box-shadow: 0px 2px 6px rgba(84, 105, 119, 0.101961); 
    p{
      font-weight: 500;
      font-size: 14px;
      line-height: 18px;
      color: $black;
    }
    .switch {
      position: relative;
      display: inline-block;
      width: 40px;
      height: 25px;
      .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        -webkit-transition: .4s;
        transition: .4s;
        &:before {
          position: absolute;
          content: "";
          height: 20px;
          width: 20px;
          left: 2px;
          bottom: 3px;
          background-color: white;
          -webkit-transition: .4s;
          transition: .4s;
        }
        &.round {
          border-radius: 34px;
          &:before {
            border-radius: 50%;
          }
        }
      }
      input:checked + .slider {
        background-color: #2196F3;
      }
      input:focus + .slider {
        box-shadow: 0 0 1px #2196F3;
      }
      input:checked + .slider:before {
        -webkit-transform: translateX(15px);
        -ms-transform: translateX(15px);
        transform: translateX(15px);
      }
    }
  }
  .info{
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 12px;
    color: rgba($color: $black, $alpha: 0.6);
  }
  .setting-list{
    li{
      cursor: move;
      +li{
        margin-top: 8px;
      }
      &.over{
        border-top: 1px solid $blue;
      }
      &.dragElem {
        opacity: 0.4;
      }
      p{
        display: flex;
        align-items: center;
        background: $white;
        border-radius: 8px;
        padding: 11px 16px;
        box-shadow: 0px 2px 6px rgba(84, 105, 119, 0.101961);
      }
      span{
        margin-right: 16px;
      }
    }
  }
}