.multi-progress-bar{
  position: relative;
  &.green{
    .primary-text{
      color: $green-card-text;
    }
    .secondary-text{
      color: rgba($color: $green-card-text, $alpha: 0.6);
    }
    .progress-label{
      &:after{
        background-image: get-icon('triangle', $green-card-text);
      }
    }
    .progress-second-track{
      background: rgba($color: $green-card-text, $alpha: 0.3);
    }
    .progress-third-track{
      background: $green-card-text;
    }
  }
  &.yellow{
    .primary-text{
      color: $yellow-card-text;
    }
    .secondary-text{
      color: rgba($color: $yellow-card-text, $alpha: 0.6);
    }
    .progress-label{
      &:after{
        background-image: get-icon('triangle', $yellow-card-text);
      }
    }
    .progress-second-track{
      background: rgba($color: $yellow-card-text, $alpha: 0.3);
    }
    .progress-third-track{
      background: $yellow-card-text;
    }
  }
  &.blue{
    .primary-text{
      color: $blue-card-text;
    }
    .secondary-text{
      color: rgba($color: $blue-card-text, $alpha: 0.6);
    }
    .progress-label{
      &:after{
        background-image: get-icon('triangle', $blue-card-text);
      }
    }
    .progress-second-track{
      background: rgba($color: $blue-card-text, $alpha: 0.3);
    }
    .progress-third-track{
      background: $blue-card-text;
    }
  }
  &.red{
    .primary-text{
      color: $red-card-text;
    }
    .secondary-text{
      color: rgba($color: $red-card-text, $alpha: 0.6);
    }
    .progress-label{
      &:after{
        background-image: get-icon('triangle', $red-card-text);
      }
    }
    .progress-second-track{
      background: rgba($color: $red-card-text, $alpha: 0.3);
    }
    .progress-third-track{
      background: $red-card-text;
    }
  }
  &.light-red{
    .primary-text{
      color: $red-card-text;
    }
    .secondary-text{
      color: rgba($color: $red-card-text, $alpha: 0.6);
    }
    .progress-label{
      &:after{
        background-image: get-icon('triangle', $red-card-text);
      }
    }
    .progress-second-track{
      background: rgba($color: $red-card-text, $alpha: 0.3);
    }
    .progress-third-track{
      background: $red-card-footer;
    }
  }
  &.light-green{
    .primary-text{
      color: $green-card-text;
    }
    .secondary-text{
      color: rgba($color: $green-card-text, $alpha: 0.6);
    }
    .progress-label{
      &:after{
        background-image: get-icon('triangle', $green-card-text);
      }
    }
    .progress-second-track{
      background: rgba($color: $green-card-text, $alpha: 0.3);
    }
    .progress-third-track{
      background: $green-card-footer;
    }
  }
  &.light-yellow{
    .primary-text{
      color: $yellow-card-text;
    }
    .secondary-text{
      color: rgba($color: $yellow-card-text, $alpha: 0.6);
    }
    .progress-label{
      &:after{
        background-image: get-icon('triangle', $yellow-card-text);
      }
    }
    .progress-second-track{
      background: rgba($color: $yellow-card-text, $alpha: 0.3);
    }
    .progress-third-track{
      background: $yellow-card-footer;
    }
  }
  &.no-label{
    padding-bottom: 0 !important;
    .progress-header{
      padding-bottom: 16px;
    }
  }
  &.big-size{
    margin-top: 32px !important;
    .progress-main-track{
      height: 13px;
      width: 100%;
    }
    .progress-label{
      &.down{
        top: 100%;
        right: initial;
        left: 90%;
        align-items: flex-start;
      }
    }
  }
  .progress-header{
    position: relative;
    padding-bottom: 22px;
  }
  .progress-title{
    font-size: 12px;
    line-height: 16px;
    span{
      +span{
        margin-left: 4px;
      }
    }
  }
  .progress-label{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    &.initial{
      align-items: flex-start;
      left: 0;
      right: initial;
    }
    &.top{
      right: 0;
      top: 0;
      padding-bottom: 16px;
      
      &::after{
        transform: rotate(180deg);
        bottom: 0;
        top: initial;
      }
    }
    &.down{
      top: 10px;
      right: 0;
      padding-top: 16px;
    }
    &::after{
        position: absolute;
        content: '';
        top: 0;
        width: 12px;
        height: 12px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }
    p{
        width: max-content;
    }
  }
  .progress-main-track{
    width: 90%;
    height: 8px;
    border-radius: 8px;
    position: relative;
    &.gray{
      background: #f3f3f3;
    }
    &.white{
      background: #fff;
    }
    &.two-track{
      .progress-third-track{
        position: absolute;
        right: initial;
        left: 0;
        top: 0;
        border-radius: 8px;
        height: 100%;
      }
    }
    .progress-second-track{
      height: 8px;
      border-radius: 8px;
      position: absolute;
      left: 0;
      top: 0;
      .progress-third-track{
        height: 8px;
        border-radius: 8px;
        position: absolute;
        right: 0;
        top: 0;
      }
    }
  }
}