.relative{
  position: relative;
}
.dot-menu-drop{
  display: none;
  min-width: 155px;
  position: absolute;
  right: 22px;
  top: 0;
  background: $white;
  border-radius: 4px;
  padding: 8px 16px;
  box-shadow: 0px 0px 15px rgba(126, 126, 126, 0.25);
  border: 1px solid #E1E9EE;
  ul{
    li{
      a{
        font-size: 14px;
        padding: 8px 0;
        color: $second-body-color;
        display: block;
      }
    }
  }
}
.dot-menu-icon:hover .dot-menu-drop{
  display: block;
}
.planner-date{
  margin-top: 24px;
  margin-bottom: 16px;
  font-size: 14px;
  line-height: 12px;
  color: $black;
}
.planner-card-container{
  display: block;
  +.planner-card-container{
    margin-top: 12px;
  }
  &.green{
    .planner-card-time{
      background-color: rgba($color: $planner-green, $alpha: 0.1);
      border-color: rgba($color: $planner-green, $alpha: 0.2);
      color: $planner-green-text;
    }
    .planner-card{
      background: $planner-green;
    }
  }
  &.red{
    .planner-card-time{
      background-color: rgba($color: $planner-red, $alpha: 0.1);
      border-color: rgba($color: $planner-red, $alpha: 0.2);
      color: $planner-red-text;
    }
    .planner-card{
      background: $planner-red;
    }
  }
  .planner-card-time{
    padding: 8px 16px;
    font-size: 12px;
    border: 1px solid;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    display: inline-block;
  }
  .planner-card{
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    border-bottom-left-radius: 4px;
    padding: 16px;
    .planner-card-header{
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      .planner-card-name{
        font-size: 14px;
        line-height: 16px;
        .planner-primary-color{
          color: $white;
        }
        .planner-secondary-color{
          color: rgba($color: $white, $alpha: 0.7);
        }
      }
      .message-icon{
        background: get-icon('message-icon', $white);
      }
    }
    .planner-text{
      font-size: 12px;
      line-height: 15px;
      letter-spacing: 0.5px;
      color: $white;
      margin-top: 24px;
      margin-bottom: 12px;
    }
    .location{
      display: flex;
      align-items: center;
      font-size: 12px;
      color: rgba($color: $white, $alpha: 0.6);
      .location-icon{
        width: 16px;
        height: 16px;
        background: get-icon('location', rgba($color: $white, $alpha: 0.6));
        margin-right: 4px;
      }
    }
  }
}
.beat-planner{
  .beat-point{
    display: flex;
    align-items: center;
    font-size: 14px;
    span{
      display: contents;
    }
    +.beat-point{
      margin-top: 16px;
    }
    img{
      width: 48px;
      height: 48px;
      margin-right: 16px;
    }
  }
  .custom-primary-button{
    margin-top: 32px;
  }
  .beat-information{
    margin-top: 12px;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    color: rgba($color: $black, $alpha: 0.6);
  }
}
.beat-card{
  box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
  border-radius: 4px;
  border: 1px solid rgba(36, 97, 255, 0.1);
  padding: 16px;
  .beat-header{
    display: flex;
    align-items: center;
    .beat-action{
      display: flex;
      align-items: center;
      font-size: 12px;
      color: rgba($color: $black, $alpha: 0.6);
      +.beat-action{
        margin-left: 14px;
      }
      span{
        margin-right: 4px;
      }
      .message-icon{
        background: get-icon('message-icon', #1678fb);
      }
    }
  }
  .beat-text{
    margin-top: 24px;
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 1.2;
    color: rgba($color: $black, $alpha: 0.9);
  }
  .location{
    display: flex;
    align-items: center;
    font-size: 12px;
    color: rgba($color: $black, $alpha: 0.6);
    .location-icon{
      width: 16px;
      height: 16px;
      background: get-icon('location', rgba($color: $black, $alpha: 0.6));
      margin-right: 4px;
    }
  }
  .beat-footer{
    margin-top: 40px;
    .multi-button{
      display: flex;
      .custom-secondary-button{
        width: 100%;
        +.custom-secondary-button{
          margin-left: 12px;
        }
      }
    }
    .custom-primary-button{
      margin-top: 12px;
    }
  }
}
.meeting-detail-section{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100vh - 104px);
  .meeting-name{
    font-size: 20px;
    margin-bottom: 32px;
  }
  .meeting-detail{
    .location{
      display: flex;
      align-items: center;
      font-size: 14px;
      +.location{
        margin-top: 16px;
      }
      .location-icon{
        background: get-icon('location', rgba($color: $black, $alpha: 0.6));
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
      }
      span{
        margin-right: 8px;
      }
    }
  }
  .meeting-agenda{
    font-size: 14px;
    line-height: 22px;
    margin-top: 32px;
    color: rgba($color: $black, $alpha: 0.6);
  }
  .meeting-detail-footer{
    a+a{
      margin-top: 16px;
    }
  }
}
.beat-history{
  padding: 0 20px 16px;
  border-bottom: 1px solid rgba($color: $black, $alpha: 0.1);
  +.beat-history{
    margin-top: 16px;
  }
  .beat-history-detail{
    .beat-time{
      font-size: 10px;
      line-height: 12px;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      margin-bottom: 16px;
      color: rgba($color: $black, $alpha: 0.6);
    }
    .beat-header,
    .beat-footer{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .beat-header{
      margin-bottom: 8px;
      .beat-name{
        font-size: 14px;
        color: rgba($color: $black, $alpha: 0.9);
        .small-text{
          font-size: 12px;
          color: rgba($color: $black, $alpha: 0.6);
        }
      }
      .rating-number{
        display: flex;
        align-items: center;
        font-size: 14px;
        font-weight: 700;
        color: rgba($color: $black, $alpha: 0.9);
        span{
          margin-right: 4px;
        }
      }
    }
    .beat-desc{
      font-size: 14px;
      color: rgba($color: $black, $alpha: 0.6);
      margin-bottom: 24px;
    }
    .beat-footer{
      .location{
        display: flex;
        align-items: center;
        font-size: 12px;
        color: rgba($color: $black, $alpha: 0.6);
        .location-icon{
          width: 16px;
          height: 16px;
          margin-right: 4px;
          background: get-icon('location', rgba($color: $black, $alpha: 0.6));
        }
      }
      .view-more{
        font-size: 12px;
        font-weight: 600;
        color: #1678FB;
      }
    }
  }
}
.no-location{
  .search-label{
    margin-bottom: 4px;
  }
  .location-list{
    margin-left: -20px;
    margin-right: -20px;
    &.no-border{
      li{
        border-bottom: none;
      }
    }
    li{
      padding: 16px 20px;
      border-bottom: 1px solid rgba($color: $black, $alpha: 0.1);
      p{
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 18px;
        color: $black;
        .location-icon{
          background: get-icon('location', rgba($color: $black, $alpha: 0.6));
          background-repeat: no-repeat;
          background-size: cover;
          margin-right: 16px;
        }
      }
    }
  }
}
.add-team{
  .search-bar{
    margin-top: 0;
    input[type="text"]{
      padding-right: 7px;
    }
  }
  .recent-team-list{
    margin: 0 -20px;
    padding-bottom: 16px;
    border-bottom: 1px solid rgba($color: $black, $alpha: 0.1);
    max-height: 300px;
    overflow-y: auto;
    li{
      padding: 0 20px;
      +li{
        margin-top: 26px;
      }
      .add-team-link{
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        .name-with-chips{
          display: flex;
          align-items: center;
          .team-name{
            color: $black;
            font-size: 14px;
            line-height: 18px;
            span{
              color: rgba($color: $black, $alpha: 0.6);
            }
          }
          .review-card-chips{
            margin-left: 8px;
            background: transparent;
          }
        }
        .plus-icon{
          background: get-icon('plus-icon', $black);
        }
      }
    }
  }
  .team-with-icon{
    margin-top: 32px;
    .team-list-body{
      display: flex;
      flex-wrap: nowrap;
      overflow-x: auto;
      .team-member{
        display: flex;
        flex-direction: column;
        align-items: center;
        +.team-member{
          margin-left: 24px;
        }
        img{
          width: 48px;
          height: 48px;
        }
        .member-name{
          font-size: 14px;
          line-height: 18px;
          max-width: 66px;
          text-overflow: ellipsis;
          overflow-x: hidden;
          white-space: nowrap;
          margin: 16px 0 8px;
        }
        .review-card-chips{
          background: transparent;
        }
      }
    }
  }
}