.profile-card{
  border-radius: 4px;
  padding: 16px;
  position: relative;
  &.superstar{
    background: linear-gradient(99.82deg, #C92C9D 7.92%, #1E90C7 93.03%);
    .profile-card-header{
      .user-icon{
        &:after{
          position: absolute;
          content: '';
          background: get-icon('super-star-crown');
          background-position: center;
          background-size: cover;
          background-repeat: no-repeat;
          display: inline-block;
          width: 35px;
          height: 35px;
          top: -8px;
          left: 3px;
          transform: rotate(-25deg);
        }
      }
    }
  }
  &.shiningstar{
    background: linear-gradient(99.82deg, #C92C9D 7.92%, #1E90C7 93.03%);
  }
  &.risingstar{
    background: #8C50AC;
  }
  &.newbies{
    background: #3493DF;
  }
  .profile-card-header{
    display: flex;
    align-items: center;
    .user-icon{
      width: 70px;
      height: 70px;
      position: relative;
      img{
        width: 70px;
        height: 70px;
      }
      .edit-profile{
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $white;
        border-radius: 50%;
        padding: 2px;
        position: absolute;
        top: 5px;
        right: 0;
        .edit-icon{
          background: get-icon('edit-icon', $blue);
          background-position: center;
          background-size: cover;
          background-repeat: no-repeat;
        }
      }
    }
    .user-detail{
      margin-left: 25px;
      .user-name{
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 4px;
        color: rgba($color: $white, $alpha: 0.9);
      }
      .user-position{
        font-size: 14px;
        font-weight: 400;
        color: rgba($color: $white, $alpha: 0.9);
        margin-bottom: 4px;
      }
      .team{
        display: flex;
        align-items: center;
        font-size: 14px;
        color: rgba($color: $white, $alpha: 0.9);
        font-weight: 400;
        .team-icon{
          background: get-icon('team', rgba($color: $white, $alpha: 0.9));
          margin-right: 4px;
        }
      }
    }
  }
  .profile-footer{
    padding-top: 12px;
    margin-top: 24px;
    border-top: 1px solid rgba(255, 255, 255, 0.3);
    display: flex;
    align-items: center;
    justify-content: space-between;
    .score-body{
      display: flex;
      align-items: center;
      .score-title{
        font-size: 14px;
        color: rgba($color: $white, $alpha: 0.9);
        margin-right: 8px;
      }
      .score-count{
        display: flex;
        align-items: center;
        font-size: 14px;
        font-weight: 700;
        color: rgba($color: $white, $alpha: 0.9);
        .score-icon{
          width: 16px;
          height: 16px;
          margin-right: 4px;
        }
        img{
          width: 16px;
          height: 16px;
          margin-right: 5px;
        }
      }
    }
    .profile-designation{
      font-size: 14px;
      line-height: 20px;
      color: rgba($color: $white, $alpha: 0.9);
    }
  }
}
.streak-card{
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  padding: 16px;
  border-radius: 4px;
  background: #E9F3FB;
  .streak{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 33.3333%;
    width: 100%;
    margin-bottom: 16px;
    &.active{
      .streak-time,
      .streak-name{
        color: rgba($color: $black, $alpha: 0.9);
      }
    }
    .rounded-circle{
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba($color: $white, $alpha: 0.7);
      width: 50px;
      height: 50px;
      margin-bottom: 16px;
    }
    img{
      width: 36px;
      height: 36px;
    }
    .streak-name{
      font-size: 12px;
      color: rgba($color: $black, $alpha: 0.4);
      margin-bottom: 8px;
      text-align: center;
    }
    .streak-time{
      font-size: 14px;
      font-weight: 700;
      color: rgba($color: $black, $alpha: 0.4);
    }
  }
}
.badges-card{
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  padding: 16px;
  border-radius: 4px;
  background: #E9F3FB;
  .badge{
    max-width: 33.3333%;
    width: 100%;
    margin-bottom: 32px;
    &.active{
      .badge-name{
        color: rgba($color: $black, $alpha: 0.9);
      }
    }
    img{
      width: 36px;
      height: 36px;
    }
    .rounded-circle{
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba($color: $white, $alpha: 0.7);
      width: 50px;
      height: 50px;
      margin: 0 auto 16px;
    }
    .badge-name{
      font-size: 12px;
      font-weight: 400;
      color: rgba($color: $black, $alpha: 0.4);
      white-space: break-spaces;
    }
  }
}