.map-header{
  background: transparent;
  &.sales-header{
    .header-search-filter{
      padding: 16px 20px;
      border-top: 1px solid #EDEDED;
      .search-list{
        li{
          display: flex;
          align-items: center;
          +li{
            margin-top: 16px;
          }
          .review-owner-name{
            font-size: 12px;
            margin-right: 8px;
            span{
              color: rgba($color: $black, $alpha: 0.6);
            }
          }
        }
      }
    }
  }
  .rounded-menu{
    width: 34px;
    height: 34px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
    border: 1px solid #F3F3F3;
    border-radius: 50%;
    background: $white;
    &.blue{
      background: $blue;
      border: 1px solid $blue;
    }
  }
  .main-map-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .nav-pills{
      background: #FCFCFC;
      border: 1px solid #F3F3F3;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
      border-radius: 4px;
      @include iphone-5(){
        flex-wrap: nowrap;
      }
      .nav-item{
        .nav-link{
          padding: 7px 30px;
          border-radius: 4px;
          font-size: 14px;
          line-height: 20px;
          font-weight: 400;
          color: #2e2e2e;
          @include iphone-5(){
            padding: 5px 20px;
            font-size: 12px;
          }
          &.active{
            font-weight: 500;
            color: $white;
          }
        }
      }
    }
  }
  .search-icon{
    margin-top: 12px;
    .map-search-button{
      padding: 4px 4px 4px 16px;
      background: $white;
      border-radius: 20px;
      box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
      border: 1px solid #F3F3F3;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .searched-name{
        display: flex;
        align-items: center;
        p{
          padding: 0 16px;
          &:first-child{
            padding-left: 12px;
          }
          +p{
            border-left: 1px solid #f3f3f3;
            max-width: 124px;
            text-overflow: ellipsis;
            overflow-x: hidden;
            white-space: nowrap;
          }
        }
      }
      .search-found{
        font-size: 12px;
        line-height: 24px;
      }
    }
    .rounded-menu{
      .search-button-icon{
        background: get-icon('search-icon', $white);
      }
    }
  }
  .map-searchbar-body{
    background: #FFFFFF;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
    border-radius: 20px;
    padding: 4px;
    width: 100%;
    display: none;
    .map-searchbar{
      position: relative;
      padding: 9px 20px 0;
      &:before{
        position: absolute;
        content: '';
        width: 24px;
        height: 24px;
        background: get-icon('search-icon', #ccc);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
      }
      input[type="text"]{
        font-size: 14px;
        border: none;
        padding-left: 36px;
        padding-bottom: 10px;
      }
    }
    .header-search-filter{
      margin-top: 4px;
      .chips-radio{
        margin-left: -2px;
        margin-right: -2px;
        .radio-label{
          max-width: initial;
          padding-left: 2px;
          padding-right: 2px;
          .radio-text{
            padding: 6px 35px;
            color: $black;
            font-weight: 400;
            line-height: 20px;
            border: none;
            border-radius: 20px;
            height: 30px;
            background: #f6f6f6;
          }
        }
        input[type="radio"]{
          &:checked{
            ~.radio-text{
              background: #E9EFFF;
              color: $blue;
              font-weight: 600;
            }
          }
        }
      }
    }
  }
}