.leaderboard{
  .leaderboard-list{
    background: #E9F3FB;
    padding: 16px;
    border-radius: 4px;
    li{
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 16px;
      border-bottom: 1px solid #DCE8F2;
      &:last-child{
        border-bottom: none;
        padding-bottom: 0;
      }
      +li{
        margin-top: 16px;
      }
      .lead-number{
        font-weight: 600;
        font-size: 14px;
        min-width: 20px;
      }
      .lead-user-body{
        display: flex;
        align-items: center;
        .user{
          margin-left: 12px;
          margin-right: 12px;
          width: 32px;
          height: 32px;
        }
        .lead-name{
          font-size: 14px;
          line-height: 20px;
          color: rgba($color: $black, $alpha: 0.9);
          display: flex;
          align-items: center;
          span{
            &:nth-child(1){
              max-width: 102px;
              overflow-x: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          }
        }
        .lead-position{
          font-size: 12px;
          line-height: 15px;
          color: rgba($color: $black, $alpha: 0.6);
          margin-top: 4px;
        }
      }
      .reward-chips{
        background: #1F0C93;
        border-radius: 4px;
        padding: 5px 12px;
        display: flex;
        align-items: center;
        @include iphone-5(){
          margin: 10px 0 0 32px;
        }
        img{
          width: 16px;
          height: 16px;
          margin-right: 5px;
        }
        .reward-icon{
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background: rgba($color: $white, $alpha: .3);
        }
        .count{
          font-size: 14px;
          font-weight: 700;
          color: rgba($color: $white, $alpha: 0.9);
        }
      }
    }
  }
}