.salesmen-body{
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 28px;
  margin-top: 28px;
  border-top: 1px solid rgba($color: $black, $alpha: 0.1);
  .salesmen-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    .name{
      font-size: 14px;
      line-height: 17px;
      color: $black;
      font-weight: 700;
    }
    .salesmen-status{
      display: flex;
      align-items: center;
      color: rgba($color: $black, $alpha: 0.6);
      img{
        width: 24px;
        height: 24px;
        font-size: 12px;
      }
    }
  }
  .salesmen-progress{
    $rssm-circular-width: 48px;
    li{
      display: flex;
      +li{
        margin-top: 22px;
      }
      .sales-circular{
        .position{
          .center-content{
            display: none;
          }
          .inside-circle{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 99;
            display: flex;
            flex-direction: column;
            align-items: center;
            .bold{
              font-size: 14px;
              color: $black;
            }
            .light-gray{
              font-size: 10px;
              color: rgba($color: $black, $alpha: 0.4);
              margin-top: 4px;
            }
          }
        }
      }
      .sales-detail{
        margin-left: 32px;
        .progress-title{
          font-size: 14px;
          line-height: 17px;
          color: rgba($color: $black, $alpha: 0.4);
        }
        .progress-percent{
          margin-top: 8px;
          font-size: 14px;
          .light-gray{
            color: rgba($color: $black, $alpha: 0.6);
          }
        }
      }
    }
  }
}