.tracking-list{
  ul{
    li{
      display: flex;
      align-items: flex-start;
      padding-bottom: 16px;
      border-bottom: 1px solid rgba($color: $black, $alpha: 0.1);
      +li{
        margin-top: 16px;
      }
      .track-time{
        background: #0F5598;
        color: $white;
        border-radius: 25px;
        padding: 6px 12px;
        font-size: 12px;
        line-height: 20px;
        font-weight: 500;
      }
      .track-location-detail{
        padding-left: 16px;
        p{
          font-size: 14px;
          line-height: 17px;
          color: rgba($color: $black, $alpha: 0.8);
          +p{
            margin-top: 16px;
          }
          &:nth-child(1){
            padding-top: 8px;
          }
        }
      }
    }
  }
}
.floating-card{
  position: absolute;
  top: 8px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
}
.map-achievement-card{
  background: #E9F3FB;
  border-radius: 4px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
  padding: 16px;
  .achievement-title{
    letter-spacing: 0.005em;
    text-transform: uppercase;
    color: rgba(0, 81, 139, 0.7);
    font-size: 10px;
    font-weight: 700;
    line-height: 18px;
    margin-bottom: 16px;
  }
  .review-card-data-body{
    display: flex;
    +.review-card-data-body{
      margin-top: 16px;
    }
    .review-data{
      width: 50%;
      text-align: left;
      .review-count{
        font-size: 14px;
        color: #00518b;
        span{
          font-weight: 400;
          color: rgba($color: #00518b, $alpha: 0.6);
        }
      }
    }
  }
}
