$feed-yellow: #F0AE16;
$feed-dark-blue: #0A8CCA;
$feed-medium-blue: #55AFDA;
$feed-light-blue: #7AC4E8;
$feed-sky-blue: #4CB8EB;
$feed-dark-green: #0D9E96;
$feed-medium-green: #1EADA5;
$feed-light-green: #4CC6BF;
$feed-peach: #F58C76;
$feed-pink: #FF7FA2;
$feed-dark-pink: #ED4E7F;
$feed-purple: #7373B3;
$feed-light-purple: #7373D3;
$feed-magenta: #D06DB4;
$feed-dark-navy: #5F5CD1;
$feed-medium-navy: #6F6CE8;
$feed-light-navy: #8D8BE0;

.newbie-label{
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  position: relative;
  &.blue{
    color: rgba($color: #0A8CCA, $alpha: 0.9);
  }
  &.rising{
    background: linear-gradient(to right, #C92C9D, #1e90c7);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  &.shining{
    background: linear-gradient(to right, #c92c9d, #f0ae16, #1e90c7);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .crown{
    position: absolute;
    top: -16px;
    left: -16px;
    transform: rotate(-25deg);
  }
}
.feed-card-section{
  padding-bottom: 28px;
  overflow-y: auto;
  max-height: calc(100vh - 150px);
  &.with-crown{
    .heading-with-drop{
      padding-top: 10px;
    }
  }
  &.small-section{
    .heading-with-drop{
      display: none;
    }
    &.ova{
      .heading-with-drop{
        display: flex;
      } 
    }
    .feed-card-container{
      flex-wrap: nowrap;
      justify-content: space-between;
      .feed-card-col{
        height: 10px;
        .feed-card{
          padding: 0;
          height: 10px;
          border-radius: 2px;
          min-height: initial;
          img{
            display: none;
          }
          .feed-card-text{
            display: none;
          }
        }
      }
    }
  }
  .bar-chart-body{
    padding-bottom: 40px;
  }
}
.feed-card-container{
  margin: 0 -4px;
  display: flex;
  flex-wrap: wrap;
  .feed-card-col{
    padding: 2px;
    width: 100%;
    max-width: 33.33%;
    .feed-card{
      width: 100%;
      height: 100%;
      min-height: 100px;
      border-radius: 4px;
      background: #e4e4e4;
      padding: 8px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      &.active{
        justify-content: flex-start;
        &.yellow{
          background: $feed-yellow;
        }
        &.dark-blue{
          background: $feed-dark-blue;
        }
        &.medium-blue{
          background: $feed-medium-blue;
        }
        &.light-blue{
          background: $feed-light-blue;
        }
        &.sky-blue{
          background: $feed-sky-blue;
        }
        &.purple{
          background: $feed-purple;
        }
        &.light-purple{
          background: $feed-light-purple;
        }
        &.dark-pink{
          background: $feed-dark-pink;
        }
        &.light-pink{
          background: $feed-pink;
        }
        &.peach{
          background: $feed-peach;
        }
        &.magenta{
          background: $feed-magenta;
        }
        &.dark-green{
          background: $feed-dark-green;
        }
        &.medium-green{
          background: $feed-medium-green;
        }
        &.light-green{
          background: $feed-light-green;
        }
        &.dark-navy{
          background: $feed-dark-navy;
        }
        &.medium-navy{
          background: $feed-medium-navy;
        }
        &.light-navy{
          background: $feed-light-navy;
        }
        &.yellow-gradient{
          background: linear-gradient(132.76deg, #F0AE16 0%, #F07F16 100%);
        }
        &.peach-gradient{
          background: linear-gradient(134.15deg, #FD9782 -2.46%, #F26D51 100%);
        }
        &.dark-pink-gradient{
          background: linear-gradient(130.84deg, #F76588 0%, #DB1D4B 96.99%);
        }
        &.light-pink-gradient{
          background: linear-gradient(132.76deg, #FF93B0 0%, #FF4B7D 100%);
        }
        &.light-navy-gradient{
          background: linear-gradient(134.96deg, #B5B4F2 -4%, #7A77D6 100%);
        }
        &.medium-navy-gradient{
          background: linear-gradient(132.76deg, #8293ED 0%, #5D5AE3 100%);
        }
        &.dark-navy-gradient{
          background: linear-gradient(132.76deg, #7284E3 0%, #6330B5 100%);
        }
        &.light-blue-gradient{
          background: linear-gradient(135.96deg, #54BADB 2.84%, #36A3D8 97.64%);
        }
        &.medium-blue-gradient{
          background: linear-gradient(134.16deg, #4DB0E8 0%, #0A79CA 97.56%);
        }
        &.dark-blue-gradient{
          background: linear-gradient(134.16deg, #3B9FD8 0%, #133BCA 97.56%);
        }
        &.light-green-gradient{
          background: linear-gradient(132.76deg, #83E1BF 0%, #32B7AF 100%);
        }
        &.medium-green-gradient{
          background: linear-gradient(132.76deg, #39C8B7 0%, #338DBF 100%);
        }
        &.dark-green-gradient{
          background: linear-gradient(132.76deg, #3ABFAF 0%, #2F61AC 100%);
        }
        &.shine-1{
          background: #F0AE16;
        }
        &.shine-2{
          background: #F58C76;
        }
        &.shine-3{
          background: #7373B3;
        }
        &.shine-4{
          background: #ED4E7F;
        }
        &.shine-5{
          background: #F0AE16;
        }
        &.shine-6{
          background: #D06DB4;
        }
        &.shine-7{
          background: #8D8BE0;
        }
        &.shine-8{
          background: #6F6CE8;
        }
        &.shine-9{
          background: #5F5CD1;
        }
        &.shine-10{
          background: #7AC4E8;
        }
        &.shine-11{
          background: #55AFDA;
        }
        &.shine-12{
          background: #0A8CCA;
        }
        &.super-1{
          background:#F76588;
        }
        &.super-2{
          background:#F58C76;
        }
        &.super-3{
          background:#F0AE16;
        }
        &.super-4{
          background:#4CC6BF;
        }
        &.super-5{
          background:#1EADA5;
        }
        &.super-6{
          background:#0D9E96;
        }
        &.super-7{
          background:#8D8BE0;
        }
        &.super-8{
          background:#6F6CE8;
        }
        &.super-9{
          background:#5F5CD1;
        }
        &.super-10{
          background:#7AC4E8;
        }
        &.super-11{
          background:#55AFDA;
        }
        &.super-12{
          background:#0A8CCA;
        }

        img{
          display: block;
        }
        .feed-card-text{
          color: $white;
          font-weight: 500;
        }
      }
      img{
        width: 32px;
        height: 32px;
        margin-bottom: 2px;
        display: none;
      }
      .feed-card-text{
        max-width: 88px;
        font-size: 12px;
        line-height: 16px;
        text-align: center;
      }
    }
  }
}

.bottom-feed{
  background: $white;
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
  width: 100%;
  max-width: 375px;
  bottom: 30px;
  top: 0;
  position: absolute;
  z-index: 2;
  transition: all 0.3s;
  transform: translateY(90%);
  @include tablet(){
    max-width: 768px;
  }
  @include mobile(){
      max-width: 480px;
  }
  &.scroll-up{
    transform: translateY(115px);
    max-height: initial;
    .new-post-button{
      display: none;
    }
  }
  .search-bar input[type=text]{
    padding: 12px 28px 12px 14px;
    font-size: 14px;
  }
  .new-post-button{
    position: absolute;
    bottom: calc(100% + 8px);
    left: 50%;
    transform: translateX(-50%);
    font-size: 14px;
    font-weight: 700;
    color: rgba($color: $black, $alpha: 0.9);
    padding: 12px 16px;
    box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
    background: $white;
    border-radius: 4px;
    cursor: pointer;
  }
  .close-bar-body{
    display: flex;
    justify-content: center;
    padding: 16px 0;
    position: absolute;
    left: 50%;
    width: 100%;
    transform: translateX(-50%);
    .close-bar{
      width: 64px;
      height: 4px;
      border-radius: 4px;
      background: #e7ebef;
    }
  }
}
.feed-details{
  overflow-y: auto;
  margin-top: 40px;
  padding-top: 4px;
  padding-bottom: 100px;
  max-height: calc(100vh - 212px);
  .feed-detail-card{
    padding: 16px;
    border-radius: 8px;
    box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
    position: relative;
    +.feed-detail-card{
      margin-top: 16px;
    }
    .fd-top{
      display: flex;
      justify-content: space-between;
      .user-detail{
        display: flex;
        align-items: center;
        .user-image{
          &.crown-user{
            position: relative;
            &:after{
              position: absolute;
              content: '';
              background: get-icon('crown');
              background-position: center;
              background-size: cover;
              background-repeat: no-repeat;
              display: inline-block;
              width: 28px;
              height: 24px;
              top: -15px;
              left: -6px;
            }
          }
          img{
            width: 32px;
            height: 32px;
            border-radius: 50%;
          }
        }
        .user-name-detail{
          margin-left: 9px;
          .user-name{
            font-size: 14px;
            line-height: 20px;
            font-weight: 500;
          }
          .user-designation{
            font-size: 12px;
            line-height: 20px;
            font-weight: 400;
            color: rgba($color: $black, $alpha: 0.6);
          }
        }
      }
      .fd-top-extra-detail{
        display: flex;
        .timing{
          font-size: 14px;
          color: rgba(0, 0, 0, 0.6);
          margin-right: 8px;
        }
        .dot-menu-icon{
          &:hover{
            .dot-menu-drop{
              display: block;
            }
          }
        }
        .dot-menu-drop{
          display: none;
          min-width: 155px;
          position: absolute;
          right: 22px;
          top: 0;
          background: $white;
          border-radius: 4px;
          padding: 8px 16px;
          box-shadow: 0px 0px 15px rgba(126, 126, 126, 0.25);
          border: 1px solid #E1E9EE;
          ul{
            li{
              a{
                font-size: 14px;
                padding: 8px 0;
                color: $second-body-color;
                display: block;
              }
            }
          }
        }
      }
    }
    .feed-post-text{
      font-size: 14px;
      line-height: 20px;
      color: rgba($color: $black, $alpha: 0.9);
      margin: 24px 0;
    }
    .fd-bottom{
      border-top: 1px solid rgba(0, 0, 0, 0.1);
      padding-top: 12px;
      .like-counter{
        font-size: 12px;
        color: rgba($color: $black, $alpha: 0.6);
        display: flex;
        align-items: center;
        .bold{
          color: #494949;
          margin: 0 3px;
        }
        .thumb-up-icon{
          width: 16px;
          height: 16px;
          background: get-icon('thumb-up', rgba($color: $black, $alpha: 0.6) );
          margin-right: 4px;
        }
      }
      .view-comment-link{
        font-size: 12px;
        line-height: 20px;
        color: rgba(73, 73, 73, 0.6);
        margin: 8px 0;
        display: block;
      }
      .fd-action-button{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .like-button{
          display: flex;
          align-items: center;
          color: $blue;
          font-size: 12px;
          font-weight: 500;
          .thumb-up-icon,
          .message-icon{
            width: 24px;
            height: 24px;
            margin-right: 6px;
          }
          .liked-icon{
            width: 16px;
            height: 16px;
            margin-right: 6px;
          }
          .thumb-up-icon{
            background: get-icon('thumb-up', $blue);
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
          }
          .message-icon{
            background: get-icon('message-icon', $blue);
          }
        }
      }
    }
  }
}