.filter-section{
  padding-left: 20px;
  padding-right: 20px;
  +.filter-section{
    margin-top: 16px;
    padding-top: 24px;
    border-top: 1px solid rgba($color: $black, $alpha: 0.1);
  }
  .search-categories{
    +.search-categories{
      margin-top: 24px;
    }
  }
  .search-bar{
    border: 1px solid #ededed;
    padding: 8px 16px;
    border-radius: 4px;
    margin: 16px 0 24px;
    &:before{
      position: absolute;
      content: '';
      width: 24px;
      height: 24px;
      background: get-icon('search-icon', #ccc);
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
    input[type="text"]{
      font-size: 14px;
      padding-left: 32px;
      border: none;
    }
  }
}
.filter-title{
  font-weight: 600;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 0.5px;
}
.filter-sub-title{
  font-size: 10px;
  line-height: 16px;
  color: rgba($color: $black, $alpha: 0.6);
  margin-top: 12px;
}
.custom-radio-list{
  margin-top: 24px;
  .circular-radio{
    +.circular-radio{
      margin-top: 16px;
    }
    input[type="radio"]:checked + label{
      &::after{
        display: block;
      }
    }
    label{
      position: relative;
      padding-left: 50px;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.5px;
      text-transform: uppercase;
      color: $black;
      &::before{
        position: absolute;
        content: '';
        width: 16px;
        height: 16px;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        border: 1px solid rgba(36, 97, 255, 0.3);
        border-radius: 50%;
      }
      &::after{
        position: absolute;
        content: '';
        width: 10px;
        height: 10px;
        left: 3px;
        top: 50%;
        transform: translateY(-50%);
        background: $blue;
        border-radius: 50%;
        display: none;
      }
    }
  }
}
.tick-check-list{
  margin-top: 16px;
  .name-check{
    display: flex;
    align-items: center;
    +.name-check{
      margin-top: 16px;
    }
    .review-card-chips{
      margin-left: 8px;
    }
  }
}
.apply-button-body{
  padding-left: 20px;
  padding-right: 20px;
  margin: 24px 0;
  .apply-btn{
    background: $blue;
    color: $white;
    display: block;
    font-size: 12px;
    line-height: 18px;
    font-weight: 600;
    text-align: center;
    padding: 10px 0;
    border-radius: 8px;
  }
}
.filter-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 32px 20px;
  .fh-title{
    display: flex;
    align-items: center;
    .close-icon{
      width: 12px;
      height: 12px;
      margin-right: 16px;
    }
    .fh-heading{
      font-size: 14px;
      line-height: 24px;
      letter-spacing: 0.5px;
    }
  }
  .clear-btn{
    font-weight: 600;
    font-size: 12px;
    line-height: 22px;
  }
}