.loss-dashboard{
  background: $red-card-footer;
  color: $white;
  padding: 16px;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  &.green{
    background: $green-card-footer;
  }
  &.blue{
    background: $blue-card-footer;
  }
  &.yellow{
    background: $yellow-card-footer;
  }
  &.red{
    background: $red-card-footer;
  }
  .loss-title{
    font-size: 16px;
    font-weight: 600;
  }
  .loss-amount{
    font-size: 14px;
  }
}
#my-loss{
  .chips-radio{
    overflow-x: auto;
    margin-right: 0;
    .radio-label{
      width: auto;
      max-width: unset;
    }
  }
  .filter-cards{
    .primary-card{
      max-width: 100%;
      +.primary-card{
        margin-top: 16px;
      }
      .primary-card-body{
        padding: 16px 16px 24px;
        display: flex;
        flex-direction: row;
        .price{
          font-size: 16px;
        }
        .price-label{
          font-size: 12px;
          margin-top: 5px;
        }
        .right{
          margin-left: 32px;
        }
      }
      .primary-card-footer{
        display: flex;
        align-items: center;
        justify-content: space-between;
        p{
          display: flex;
          align-items: center;
          img{
            width: 24px;
            height: 24px;
            margin-left: 4px;
          }
          span{
            +span{
              margin-left: 4px;
            }
          }
        }
        .secondary-color{
          color: rgba($color: $white, $alpha: .8);
        }
        .trending-label{
          display: flex;
          align-items: center;
          font-size: 12px;
          font-weight: 400;
          .tail-arrow{
            width: 24px;
            height: 24px;
          }
        }
      }
    }
  }
}
.bar-chart-body{
  margin-top: 32px;
}
.chartWrapper {
  position: relative;
  > canvas {
    position: absolute;
    left: 0;
    top: 0;
    pointer-events:none;
  }
  .chartAreaWrapper {
    width: 600px;
    overflow-x: scroll;
  }
}