.chat-listing{
  li{
    border-bottom: 1px solid rgba($color: $black, $alpha: 0.1);
    a{
      padding: 16px 20px;
      display: flex;
      align-items: center;
      width: 100%;
      img{
        width: 48px;
        height: 48px;
        @include iphone-5(){
          width: 32px;
          height: 32px;
        }
      }
      .content-body{
        padding: 0 0 0 12px;
        width: 100%;
        .name-body{
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 5px;
          .name{
            font-weight: 600;
            font-size: 14px;
            line-height: 19px;
            color: #131314;
            padding-right: 5px;
          }
          .time{
            font-size: 12px;
            line-height: 17px;
            color: #9c9ca5;
          }
        }
        .text-body{
          display: flex;
          align-items: center;
          justify-content: space-between;
          .message{
            font-size: 14px;
            line-height: 18px;
            color: #9c9ca5;
            max-width: 220px;
            white-space: nowrap;
            overflow-x: hidden;
            text-overflow: ellipsis;
            @include iphone-5(){
              max-width: 175px;
            }
          }
          .count-badge{
            padding: 2px 5px;
            background: #E94F4F;
            color: $white;
            font-size: 9px;
            line-height: 13px;
            font-weight: 500;
            border-radius: 11px;
            min-width: 20px;
            text-align: center;
          }
        }
      }
    }
    &.new-message{
      background: #f5f5f5;
      a{
        .content-body{
          .name-body{
            .time{
              color: #131314;
            }
          }
        }
      }
    }
  }
}
.chat-window{
  height: calc(100vh - 250px);
  display: flex;
  align-items: flex-end;
}
.chating-screen{
  overflow-y: auto;
  max-height: calc(100vh - 270px);
  li{
    display: block;
    width: 100%;
    .message{
      max-width: 268px;
      padding: 10px 22px 10px 16px;
      font-size: 14px;
      line-height: 21px;
      font-weight: 400;
      display: flex;
      flex-direction: column;
      .receiver-name{
        font-weight: 500;
        font-size: 12px;
        line-height: 14px;
        color: #4589FF;
        margin-bottom: 8px;
      }
    }
    .send-detail{
      display: flex;
      align-items: center;
      font-size: 9px;
      line-height: 11px;
      color: #9C9CA5;
      margin-top: 5px;
      .read-icon{
        width: 14px;
        height: 14px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        margin-right: 6px;
        &.read{
          background-image: get-icon('read-icon');
        }
        &.unread{
          background-image: get-icon('unread-icon');
        }
      }
    }
  }
  .send{
    float: right;
    +.send{
      margin-top: 6px;
    }
    +.receive{
      margin-top: 9px;
    }
    .message{
      color: $white;
      background: #4589FF;
      box-shadow: 0px 4px 16px rgba(69, 137, 255, 0.239216);
      border-radius: 16px 16px 0px 16px;
      margin-left: auto;
    }
    .send-detail{
      float: right;
    }
  }
  .receive{
    float: left;
    position: relative;
    +.receive{
      margin-top: 6px;
    }
    +.send{
      margin-top: 9px;
    }
    .message{
      color: #131314;
      background: $white;
      box-shadow: 0px 4px 16px rgba(69, 70, 92, 0.0784314);
      border-radius: 0px 16px 16px 16px;
      margin-right: auto;
    }
    .send-detail{
      float: left;
    }
    .receiver-image{
      width: 42px;
      height: 42px;
      position: absolute;
      left: 0;
      top: 0;
      +.message{
        margin: 21px auto 0 27px;
        +.send-detail{
          margin: 5px 0 0 27px;
        }
      }
    }
  }
}
.chat-message-textbar{
  background: transparent;
  box-shadow: none;
  .comment-textbar{
    padding: 14px 20px;
    box-shadow: none;
    background: $white;
    input{
      font-size: 14px;
      line-height: 20px;
      outline: none;
      box-shadow: none;
      border: none;
      width: 100%;
    }
    .post-button{
      color: $blue;
      font-size: 12px;
      line-height: 22px;
      font-weight: 500;
    }
  }
  .map-filter-chips-container{
    margin-bottom: 8px;
    ul{
      display: flex;
      overflow-x: scroll;
      position: relative;
      li{
        max-width: initial;
        +li{
          margin-left: 8px;
        }
        &:nth-child(1){
          padding-left: 20px;
        }
        .map-filter-chips{
          color: $black;
          font-size: 12px;
          line-height: 15px;
          font-weight: 400;
          border-radius: 20px;
          border: 1px solid #D4B3FF;
          padding: 8px 16px;
          color: #000;
          background: #fff;
          display: block;
          white-space: nowrap;
          text-align: center;
          &.active{
            font-weight: 600;
            color: $white;
            background: #d4b3ff;
          }
        }
      }
    }
  }
  .textbar-control{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 20px 20px;
    background: $white;
    span{
      width: 24px;
      height: 24px;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      &.smiley{
        background-image: get-icon('smiley');
      }
      &.at{
        background-image: get-icon('@');
      }
      &.mic{
        background-image: get-icon('mic');
      }
      &.image{
        background-image: get-icon('image');
      }
      &.circular-plus{
        background-image: get-icon('circular-plus');
      }
      &.send{
        background-image: get-icon('send');
      }
    }
  }
}
.group-detail-menu{
  background: $white;
  .media-link{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 0;
    p{
      display: flex;
      align-items: center;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: $black;
      img{
        margin-right: 10px;
        width: 24px;
        height: 24px;
      }
    }
    .right-arrow{
      padding-top: 6px;
    }
  }
  .all-member{
    .label{
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      margin: 16px 0 20px;
      color: #75757c;
    }
    .add-member{
      display: flex;
      align-items: center;
      color: #1678FB;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      margin-bottom: 25px;
      .plus-icon{
        width: 18px;
        height: 18px;
        margin-right: 20px;
        background-image: get-icon('plus-icon', #1678FB);
      }
    }
    .add-member-list{
      padding-bottom: 16px;
      .member-listing{
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        +.member-listing{
          margin-top: 16px;
        }
        .user-detail{
          display: flex;
          align-items: center;
          .user-image{
            width: 32px;
            height: 32px;
            margin-right: 12px;
          }
          .user-name{
            font-size: 16px;
            line-height: 19px;
            color: #151624;
          }
        }
      }
    }
  }
}
.group-control{
  background: $white;
  padding: 0 20px;
  margin-top: 10px;
  a{
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    padding: 14px 0;
    display: block;
    +a{
      border-top: 0.15px solid #e9e9e9;;
    }
    &.normal-action{
      color: #2B7CEA;
    }
    &.clear-chat{
      color: #FF6E6E;
    }
  }
}
.created-by{
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  color: #C9C9D3;
  margin-top: 16px;
  padding: 0 20px;
}
.created-date{
  font-weight: 400;
  font-size: 11px;
  line-height: 18px;
  color: #C9C9D3;
  padding: 0 20px;
}
.bottom-message-bar{
  .create-group-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .group-left{
      display: flex;
      align-items: center;
      .group-title{
        font-size: 16px;
        line-height: 19px;
        font-weight: 700;
        margin-left: 24px;
        color: #151624;
      }
    }
    .create-group-btn{
      font-weight: 600;
      font-size: 16px;
      line-height: 20px;
      color: #1678FB;
    }
    .close-icon{
      position: static;
    }
  }
  .selected-name-list{
    padding: 12px 20px;
    background: #f3f3f5;
    margin: 24px -20px;
    ul{
      overflow-x: auto;
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      li{
        font-size: 14px;
        line-height: 18px;
        color: #131314;
        white-space: nowrap;
        +li{
          margin-left: 2px;
        }
      }
    }
  }
  .select-name{
    ul{
      max-height: 400px;
      overflow-y: auto;
      li{
        padding: 12px 0;
        border-bottom: 1px solid #EDEDF0;
        input[type="checkbox"]:checked + label {
          .circular-check{
            background-color: #1678FB;
            border-color: #1678FB;
            &::after{
              opacity: 1;
            }
          }
        }
        label{
          display: flex;
          align-items: center;
          justify-content: space-between;
          .user-left{
            display: flex;
            align-items: center;
            img{
              width: 43px;
              height: 43px;
              margin-right: 16px;
            }
            .user-detail{
              .user-name{
                font-weight: 600;
                font-size: 14px;
                line-height: 19px;
                color: #131314;
              }
              .user-position{
                font-size: 14px;
                line-height: 18px;
                color: #9c9ca5;
                margin-top: 4px;
              }
            }
          }
          .circular-check{
            position: relative;
            border: 1px solid #C9C9D3;
            border-radius: 50%;
            height: 20px;
            width: 20px;
            &:after{
              border: 2px solid #fff;
              border-top: none;
              border-right: none;
              content: "";
              height: 6px;
              opacity: 0;
              position: absolute;
              left: 4px;
              top: 5px;
              transform: rotate(-45deg);
              width: 10px;
            }
          }
        }
      }
    }
  }
}