//-- font family --//
@font-face {
  font-family: "Inter";
  src: url("../assets/fonts/Inter-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../assets/fonts/Inter-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../assets/fonts/Inter-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../assets/fonts/Inter-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../assets/fonts/Inter-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

$font-main: 'Inter';

.regular{
  font-weight: 400;
}
.medium{
  font-weight: 500;
}
.semibold{
  font-weight: 600;
}
.bold{
  font-weight: 700;
}
.text-blue{
  color: $blue;
}
.text-white{
  color: $white;
}
.text-black{
  color: $black;
}
.text-red{
  color: #F13838;
}
.italic{
  font-style: italic;
}
.gradient-text{
  background: linear-gradient(to right, #A3187B, #F0AE16, #0A90CF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}