.map{
  padding-bottom: 24px;
  .section-title{
    font-size: 16px;
  }
}
.hover-dropdown{
  position: relative;
  margin-bottom: 16px;
  .hover-dropdown-title{
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
    color: rgba($color: $black, $alpha: 0.9);
    position: relative;
    display: inline-flex;
    align-items: center;
    padding-right: 24px;
    &:after{
      position: absolute;
      content: '';
      right: 0;
      width: 16px;
      height: 16px;
      background-image: get-icon('drop-arrow', $second-body-color);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
    }
    &:hover{
      +.drop-menu-item{
        display: block;
      }
    }
  }
  .drop-menu-item{
    background: $white;
    box-shadow: 0px 0px 15px rgba(126, 126, 126, 0.25);
    border-radius: 4px;
    padding: 16px;
    width: 150px;
    display: none;
    position: absolute;
    z-index: 99;
    li{
      +li{
        margin-top: 8px;
      }
      a{
        font-size: 14px;
        line-height: 12px;
        color: $second-body-color;
      }
    }
  }
}
.channel-section{
  .ta-heading{
    margin-bottom: 22px;
    p{
      font-size: 14px;
      color: $black;
      font-weight: 400;
      .secondary-text{
        color: rgba($color: $black, $alpha: 0.6);
      }
    }
  }
  .ta-track{
    background: #f3f3f3;
    .third-track{
      background: #9ED3B0;
      position: absolute;
      left: 0;
      height: 100%;
      border-radius: 8px;
    }
  }
}
.feedback-list{
  margin-left: -20px;
  margin-right: -20px;
  border-top: 1px solid rgba($color: $black, $alpha: 0.1);
  li{
    padding: 16px 46px 16px 20px;
    border-bottom: 1px solid rgba($color: $black, $alpha: 0.1);
    position: relative;
    display: flex;
    align-items: center;
    &:after{
      position: absolute;
      right: 20px;
      content: '';
      background: get-icon('drop-arrow', $black);
      width: 16px;
      height: 16px;
      transform: rotate(-90deg);
    }
    a{
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      p{
        font-size: 14px;
        line-height: 18px;
        color: #515151;
        font-weight: 600;
      }
      .rating-number{
        color: rgba($color: $black, $alpha: 0.9);
        display: flex;
        align-items: center;
        span{
          margin-right: 4px;
        }
      }
    }
  }
}