.donut-charts{
  &.red{
    .legend-list{
      &:nth-child(1){
        .legend-left{
          &::after{
            background-color: #FB6767;
          }
        }
      }
      &:nth-child(2){
        .legend-left{
          &::after{
            background-color: #FC9898;
          }
        }
      }
      &:nth-child(3){
        .legend-left{
          &::after{
            background-color: #FEE1E1;
          }
        }
      }
    }
  }
  &.green{
    .legend-list{
      &:nth-child(1){
        .legend-left{
          &::after{
            background-color: #51BF77;
          }
        }
      }
      &:nth-child(2){
        .legend-left{
          &::after{
            background-color: #89D3A3;
          }
        }
      }
      &:nth-child(3){
        .legend-left{
          &::after{
            background-color: #DCF2E4;
          }
        }
      }
    }
  }
  .heading-with-drop{
    .section-label{
      font-size: 12px;
      color: $second-body-color;
    }
  }
  .chart{
    position: relative;
    max-width: 134px;
    height: 134px;
    margin: 24px auto 40px;
    .circle-text{
      position: absolute;
      left: 50%;
      top: 55%;
      transform: translate(-50%, -50%);
      text-align: center;
      .chart-main-text{
        font-size: 20px;
        color: rgba(0, 0, 0, 0.9);
      }
      .chart-secondary-text{
        font-size: 12px;
        margin-top: 4px;
        color: rgba(0, 0, 0, 0.7);
      }
    }
  }
  .legend-listing{
    border-bottom: 1px solid rgba($color: #000000, $alpha: 0.1);
    .legend-list{
      padding: 16px 40px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-top: 1px solid rgba($color: #000000, $alpha: 0.1);
      .legend-left{
        padding-left: 12px;
        position: relative;
        &:after{
          content: '';
          position: absolute;
          width: 4px;
          border-radius: 10px;
          top: 0;
          bottom: 0;
          left: 0;
        }
        .legend-name{
          font-size: 14px;
          color: rgba($color: #000000, $alpha: 0.9);
        }
        .legend-price{
          font-size: 12px;
          margin-top: 4px;
        }
      }
      .legend-right{
        display: flex;
        align-items: center;
        .category{
          font-size: 12px;
          color: rgba(0, 0, 0, 0.4);
          margin-right: 12px;
        }
      }
    }
  }
}