.hamburger{
  width: 24px;
  height: 24px;
  background-image: get-icon('hamburger', $black);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.notification-icon{
  width: 24px;
  height: 24px;
  background: get-icon('bell-icon', $black);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  &.new-notification{
    &:after{
      position: absolute;
      content: '';
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: #1AC887;
      top: 2px;
      right: 4px;
    }
  }
}
.dot-notification-icon{
  width: 24px;
  height: 24px;
  background: get-icon('bell-notification');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
header{
  position: fixed;
  width: 100%;
  top: 0;
  background: $white;
  padding: 24px 20px;
  z-index: 9999;
  max-width: 375px;
  @include tablet(){
    max-width: 768px;
  }
  @include mobile(){
    max-width: 480px;
  }
  &.bg-trans{
    background: transparent;
    &.white{
      background: $white;
    }
  }
  &.bg-white{
    &.white-icon{
      .hamburger{
        background-image: get-icon('hamburger', $black);
      }
      .notification-icon{
        background: get-icon('bell-icon', $black);
      }
    }
  }
  &.white-icon{
    .hamburger{
      background-image: get-icon('hamburger', $white);
    }
    .notification-icon{
      background: get-icon('bell-icon', $white);
    }
  }
  .main-navigation{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    .left{
      display: flex;
      align-items: center;
      .location{
        display: flex;
        align-items: center;
        font-size: 12px;
        color: rgba($color: $black, $alpha: 0.6);
        .location-icon{
          width: 16px;
          height: 16px;
          margin-right: 4px;
          background: get-icon('location', rgba($color: $black, $alpha: 0.6));
        }
      }
      .current-date{
        font-size: 14px;
        line-height: 20px;
        font-weight: 500;
        color: rgba($color: $black, $alpha: 0.9);
        margin-left: 16px;
      }
    }
    .right{
      display: flex;
      align-items: center;
      .reward-chips{
        background: #1F0C93;
        border-radius: 4px;
        padding: 5px 12px;
        display: flex;
        align-items: center;
        img{
          width: 16px;
          height: 16px;
          margin-right: 5px;
        }
        .reward-icon{
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background: rgba($color: $white, $alpha: .3);
        }
        .count{
          font-size: 14px;
          font-weight: 700;
          color: rgba($color: $white, $alpha: 0.9);
        }
      }
      .notification-icon{
        margin-left: 8px;
      }
    }
  }
}

//back-header
.back-header{
  &.bg-white{
    .chips-radio{
      .radio-label{
        .radio-text{
          span{
            display: none;
          }
        }
      }
    }
  }
  &.stay{
    .scroll-heading-1{
      display: block !important;
      opacity: 1 !important;
    }
    .scroll-heading-2{
      display: block !important;
      opacity: 1 !important;
      margin-top: 4px;
      color: rgba($color: $black, $alpha: 0.6);
    }
  }
  &.level-1{
    &.bg-white{
      .scroll-heading-1{
        display: block;
        opacity: 1;
      }
    }
  }
  &.level-2{
    .scroll-heading{
      .scroll-heading-2{
        display: block;
        opacity: 1;
      }
    }
    .message-icon{
      display: block;
    }
    &.bg-white{
      .scroll-heading-1{
        display: block;
        opacity: 1;
      }
      .scroll-heading-2{
        font-size: 12px;
      }
    }
  }
  .message-icon{
    display: none;
  }
  .controls{
    display: flex;
    align-items: center;
    .dot-menu-icon:hover .dot-menu-drop{
      display: block;
    }
    .direction{
      font-weight: 600;
      font-size: 12px;
      line-height: 22px;
      color: $blue;
    }
    .call-icon{
      margin: 0 8px;
      background: get-icon('call-icon', $blue); 
    }
  }
  .chips-radio{
    margin-right: 8px;
    .radio-label{
      max-width: 100%;
      input[type="checkbox"]:checked ~ .radio-text{
        background-color: rgba($color: $blue, $alpha: 0.1);
        border-color: rgba($color: $blue, $alpha: 0.1);
      }
      .radio-text{
        padding: 5px 12px;
        border-color: rgba($color: $blue, $alpha: 0.1);
        color: $blue;
        img{
          width: 24px;
          height: 24px;
        }
        span{
          display: block;
        }
      }
    }
  }
  &.back-header-only{
    .scroll-heading{
      .scroll-heading-1{
        display: block;
        opacity: 1;
      }
      &.with-emoji{
        p{
          display: flex;
          align-items: center;
          img{
            width: 32px;
            height: 32px;
            margin-left: 4px;
          }
        }
      }
    }
  }
  .scroll-heading{
    margin-left: 12px;
    .scroll-heading-1{
      font-size: 14px;
      font-weight: 500;
      line-height: 20px;
      display: none;
      opacity: 0;
      animation: fadein 0.3s;
    }
    .scroll-heading-2{
      font-size: 14px;
      display: none;
      opacity: 0;
      animation:  fadein 0.3s;
    }
  }
  .dot-menu-drop{
    display: none;
    min-width: 155px;
    position: absolute;
    right: 22px;
    top: 0;
    background: $white;
    border-radius: 4px;
    padding: 8px 16px;
    box-shadow: 0px 0px 15px rgba(126, 126, 126, 0.25);
    border: 1px solid #E1E9EE;
    ul{
      li{
        a{
          font-size: 14px;
          padding: 8px 0;
          color: $second-body-color;
          display: block;
        }
      }
    }
  }
}
.closing-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  .sales-tracking-name-body{
    display: flex;
    align-items: center;
    .sales-tracking-name{
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      margin-right: 8px;
    }
  }
}
.search-header{
  .search-bar{
    display: flex;
    align-items: center;
    &:before{
      position: absolute;
      content: '';
      width: 24px;
      height: 24px;
      background: get-icon('search-icon', rgba($color: $black, $alpha: 0.5));
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
    input{
      border: none;
      outline: none;
      box-shadow: none;
      padding: 0;
      padding-left: 40px;
      font-size: 14px;
      line-height: 18px;
    }
  }
  .search-action{
    font-size: 12px;
    line-height: 22px;
    color: #2461FF;
  }
}
.add-header{
  .close-icon{
    width: 12px;
    height: 12px;
  }
}

//side bar
.sidebar{
  position: fixed;
  height: 100vh;
  width: 100%;
  z-index: 9999;
  display: none;
  max-width: 375px;
  @include tablet(){
    max-width: 768px;
  }
  @include mobile(){
    max-width: 480px;
  }
  &.active{
    display: block;
    .sidebar-body{
      transform: translateX(0);
    }
  }
  .sidebar-body{
    background: $white;
    height: 100vh;
    padding: 20px 20px 40px;
    max-width: 288px;
    box-shadow: 0px 0px 15px rgba(126, 126, 126, 0.25);
    border: 1px solid #E1E9EE;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    position: absolute;
    z-index: 99999;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    transform: translateX(-100%);
    .sidebar-header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .sidebar-name{
        font-size: 20px;
        font-weight: 700;
        color: rgba($color: $black, $alpha: 0.9);
        line-height: 20px;
      }
      .close-icon{
        width: 16px;
        height: 16px;
      }
    }
    .sidebar-link-body{
      height: calc(100vh - 90px);
      margin-top: 30px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      ul{
        li{
          .side-menu{
            font-size: 14px;
            line-height: 12px;
            color: $second-body-color;
            padding: 8px 0;
            display: flex;
            align-items: center;
            img{
              width: 24px;
              height: 24px;
              margin-right: 12px;
            }
          }
        }
      }
    }
  }
  .sidebar-overlay{
    background-color: rgba(0,0,0,.1);
    width: 100%;
    top: 0;
    bottom: 0;
    position: absolute;
    z-index: 99;
  }
}
.feedback-header{
  .main-navigation{
    align-items: flex-start;
    .left{
      align-items: flex-start;
    }
  }
  .close-icon{
    width: 16px;
    height: 16px;
    margin-top: 3px;
  }
  .scroll-heading{
    .scroll-heading-1{
      display: block;
      opacity: 1;
      font-size: 16px;
      font-weight: 600;
    }
    .scroll-heading-2{
      font-size: 12px;
      color: rgba($color: $black, $alpha: 0.9);
    }
  }
}

.personal-chat{
  padding-bottom: 16px;
  .controls{
    .call-icon{
      margin: 0;
    }
    a{
      +a{
        margin-left: 8px;
      }
    }
  }
  .user-detail{
    display: flex;
    align-items: center;
    margin-left: 20px;
    img{
      width: 48px;
      height: 48px;
    }
    .user-name-body{
      padding-left: 12px;
      .user-name{
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        color: #151624;
      }
      .user-designation{
        font-size: 13px;
        line-height: 16px;
        color: #949494;
        margin-top: 2px;
      }
    }
  }
  .chat-title{
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 24px;
    font-weight: 700;
    color: rgba($color: $black, $alpha: 0.9);
    margin-left: 16px;
    img{
      width: 32px;
      height: 32px;
      margin-left: 4px;
    }
  }
  .filter-section{
    padding: 0;
    &.with-searchbar{
      display: none;
    }
    .search-bar{
      margin: 0;
      padding: 6px 16px;
      input[type="text"]{
        padding: 0 24px 0 28px;
      }
    }
  }
}