.create-meeting-home{
  .create-meeting-action{
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 18px;
    color: rgba($color: black, $alpha: 0.6);
    margin-left: -20px;
    margin-right: -20px;
    padding: 16px 20px;
    border-bottom: 1px solid rgba($color: black, $alpha: 0.1);
    &.no-border{
      border-bottom: none;
    }
    span{
      margin-right: 16px;
    }
    .name-with-chips{
      display: flex;
      align-items: center;
      .team-name{
        color: #000;
        font-size: 14px;
        line-height: 18px;
        span{
          color: rgba(0,0,0,.6);
        }
      }
    }
    .location-icon{
      background: get-icon('location', rgba($color: black, $alpha: 0.6));
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
    input[type="text"]{
      background: transparent;
      border: none;
      outline: none;
      box-shadow: none;
    }
  }
  .current-date{
    margin-left: -20px;
    margin-right: -20px;
    padding: 24px 20px;
    border-bottom: 1px solid rgba($color: black, $alpha: 0.1);
    font-size: 14px;
    line-height: 18px;
  }
}