.salespedia{
  .salespedia-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 18px;
    .main-tabs{
      margin-bottom: 0;
      li{
        +li{
          margin-left: 16px;
        }
      }
    }
  }
  .watch-card{
    +.watch-card{
      margin-top: 32px;
    }
    .video-card{
      position: relative;
      border-radius: 8px;
      height: 160px;
      .video-poster{
        border-radius: 8px;
        height: 100%;
        width: 100%;
      }
      .play-button{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 40px;
        height: 40px;
      }
    }
    .watch-body{
      margin-top: 16px;
      .watch-flex{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 4px;
        .watch-name{
          font-weight: 600;
          font-size: 16px;
          line-height: 20px;
          color: rgba($color: #2e2e2e, $alpha: 0.9);
        }
        .duration{
          font-size: 12px;
          line-height: 18px;
          color: #2e2e2e;
          display: flex;
          align-items: center;
          .clock-icon{
            width: 16px;
            height: 16px;
            margin-right: 4px;
          }
        }
      }
    }
    .watch-author{
      font-size: 12px;
      line-height: 18px;
      color: rgba(46, 46, 46, 0.6);
      margin-bottom: 12px;
    }
    .watch-desc{
      font-size: 12px;
      line-height: 18px;
      color: rgba(46, 46, 46, 0.6);
    }
    &.read-card{
      display: flex;
      align-items: center;
      .video-card{
        width: 90px;
        height: 90px;
        .video-poster{
          width: 90px;
          height: 90px;
          object-fit: cover;
        }
      }
      .watch-body{
        margin-top: 0;
        margin-left: 16px;
      }
    }
  }
}