#login{
  padding-top: 0;
}
.login{
  height: calc(100vh - 40px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .login-logo{
    width: 120px;
    height: 120px;
    margin: 0 auto;
    &.unilever{
      width: 56px;
      height: 60px;
    }
  }
  .login-form{
    .input-form-control{
      display: flex;
      flex-direction: column;
      +.input-form-control{
        margin-top: 24px;
      }
      label{
        font-size: 12px;
        line-height: 18px;
        color: #2e2e2e;
        margin-bottom: 8px;
      }
      .input-text{
        border-radius: 4px;
        border: 1px solid #ededed;
        padding: 10px 16px;
        font-size: 14px;
        line-height: 20px;
        outline: none;
        box-shadow: none;
        background: none;
        &.invalid{
          border-color: #FFC7C7;
        }
      }
      .input-validation{
        font-size: 12px;
        line-height: 18px;
        color: #f13838;
        margin-top: 8px;
      }
      .password-body{
        position: relative;
        input{
          width: 100%;
          padding-right: 30px;
        }
        span{
          position: absolute;
          right: 12px;
          top: 50%;
          transform: translateY(-50%);
        }
      }
      .counter{
        font-size: 14px;
        line-height: 20px;
        color: #2e2e2e;
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    .forgot-link{
      text-align: right;
      margin: 10px 0 24px;
      color: #2461FF;
      font-size: 12px;
      font-weight: 600;
      line-height: 22px;
      display: block;
    }
  }
  &.otp{
    justify-content: initial;
    height: 100%;
    padding-bottom: 30px;
    .otp-title{
      font-size: 20px;
      line-height: 20px;
      font-weight: 700;
      color: rgba($color: #2e2e2e, $alpha: 0.9);
      margin-bottom: 12px;
    }
    .otp-desc{
      font-size: 12px;
      line-height: 18px;
      color: rgba($color: #2e2e2e, $alpha: 0.5);
    }
    .login-form{
      margin-top: 32px;
    }
    .custom-primary-button{
      margin-top: 80px;
      &.disabled-button{
        background: rgba($color: #1678FB, $alpha: 0.3);
        cursor: not-allowed;
      }
    }
  }
}