.primary-card{
  border-radius: 4px;
  min-width: 156px;
//   width: 100%;
  max-width: 50%;
  margin-right: 8px;
  display: flex;
  flex-direction: column;
  .primary-card-body{
    padding: 16px;
    white-space: normal;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
      .extra-icon{
          position: absolute;
          top: 12px;
          right: 12px;
          width: 32px;
          height: 32px;
      }
      .main-title{
          margin-bottom: 55px;
          .primary-title{
              font-size: 20px;
              line-height: 32px;
              font-weight: 700;
              margin-bottom: 8px;
              display: flex;
              flex-wrap: wrap;
              align-items: center;
              img{
                  width: 31px;
                  margin-left: 5px;
              }
          }
          .secondary-title{
              font-size: 12px;
              line-height: 16px;
              font-weight: 400;
              position: absolute;
              display: flex;
              align-items: center;
              span{
                  +span{
                      margin-left: 5px;
                  }
              }
              img{
                  width: 24px;
                  height: 24px;
                  margin-right: 4px;
              }
          }
          .third-title{
              font-size: 14px;
              line-height: 16px;
              font-weight: 400;
              &.w-arrow{
                  display: flex;
                  span{
                      margin-right: 2px;
                  }
              }
          }
      }
      .progress-status{
          display: flex;
          justify-content: space-between;
          font-size: 12px;
          line-height: 15px;
          p{
              word-break: break-word;
          }
          .percentage{
              font-weight: 700;
              span{
                  font-weight: 500;
              }
          }
          .time{
              font-weight: 400;
              font-style: italic;
          }
      }
  }
  .progress-line{
      height: 4px;
  }
  .primary-card-footer{
    padding: 10px 16px;
    color: $white;
    font-size: 14px;
    line-height: 17px;
    font-weight: 500;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    p{
        text-overflow: ellipsis;
        overflow: hidden;
    }
    &.footer-top{
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        border-top-right-radius: 4px;
        border-top-left-radius: 4px;
    }
  }
  &.yellow{
      background: $yellow-card-bg;
      .primary-color{
          color: $yellow-card-text;
      }
      .secondary-color{
          color: rgba($color: $yellow-card-text, $alpha: 0.4)
      }
      .third-color{
          color: rgba($color: $yellow-card-text, $alpha: 0.6)
      }
      .progress-line{
          background: $yellow-card-progress;
      }
      .primary-card-footer{
          background-color: $yellow-card-footer;
      }
  }
  &.blue{
        background: $blue-card-bg;
        .primary-color{
            color: $blue-card-text;
        }
        .secondary-color{
            color: rgba($color: $blue-card-text, $alpha: 0.4)
        }
        .third-color{
            color: rgba($color: $blue-card-text, $alpha: 0.6)
        }
        .progress-line{
            background: $blue-card-progress;
        }
        .primary-card-footer{
            background-color: $blue-card-footer;
        }
    }
  &.green{
      background: $green-card-bg;
      .primary-color{
          color: $green-card-text;
      }
      .secondary-color{
          color: rgba($color: $green-card-text, $alpha: 0.4)
      }
      .third-color{
          color: rgba($color: $green-card-text, $alpha: 0.6)
      }
      .progress-line{
          background: $green-card-progress;
      }
      .primary-card-footer{
          background-color: $green-card-footer;
      }
  }
  &.red{
      background: $red-card-bg;
      .primary-color{
          color: $red-card-text;
      }
      .secondary-color{
          color: rgba($color: $red-card-text, $alpha: 0.4)
      }
      .third-color{
          color: rgba($color: $red-card-text, $alpha: 0.6)
      }
      .progress-line{
          background: $red-card-progress;
      }
      .primary-card-footer{
          background-color: $red-card-footer;
      }
  }
}