nav{
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background: $white;
  width: 100%;
  max-width: 375px;
  z-index: 999;
  box-shadow: 0px -5px 8px rgba(0, 0, 0, 0.05);
    @include tablet(){
        max-width: 768px;
    }
    @include mobile(){
      max-width: 480px;
    }

    ul{
      display: flex;
      align-items: center;
      justify-content: space-between;
      li{
        max-width: 20%;
        width: 100%;
        a{
          padding: 8px 0;
          display: flex;
          flex-direction: column;
          align-items: center;
          &.active{
            .nav-name{
              color: $blue;
              font-weight: 500;
            }
            .nav-icon{
              &.unilever{
                background-image: get-icon('home', $blue);
              }
              &.performance{
                background-image: get-icon('performance', $blue);
              }
              &.feed{
                background-image: get-icon('feed', $blue);
              }
              &.planner{
                background-image: get-icon('planner', $blue);
              }
              &.schedule{
                background-image: get-icon('calendar-icon', $blue);
              }
              &.maps{
                background-image: get-icon('map', $blue);
              }
              &.team{
                background-image: get-icon('team', $blue);
              }
            }
          }
          .nav-icon{
            width: 24px;
            height: 24px;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            margin-bottom: 4px;
            &.unilever{
              background-image: get-icon('home', $black);
            }
            &.performance{
              background-image: get-icon('performance', $black);
            }
            &.feed{
              background-image: get-icon('feed', $black);
            }
            &.planner{
              background-image: get-icon('planner', $black);
            }
            &.schedule{
              background-image: get-icon('calendar-icon', $black);
            }
            &.maps{
              background-image: get-icon('map', $black);
            }
            &.team{
              background-image: get-icon('team', $black);
            }
          }
          .nav-name{
            color: $black;
            font-size: 10px;
            line-height: 12px;
            font-weight: 400;
          }
        }
      }
    }
    &.comment-textbar{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 14px 20px;
      border-top: 1px solid #f3f3f3;
      box-shadow: none;
      input{
        font-size: 14px;
        line-height: 20px;
        outline: none;
        box-shadow: none;
        border: none;
        width: 100%;
        padding: 0 10px 0 0;
      }
      .post-button{
        color: $blue;
        font-size: 12px;
        line-height: 22px;
        font-weight: 500;
      }
    }
}