.wp-card{
  width: 100%;
  border-radius: 4px;
  position: relative;
  padding: 20px 16px 16px;
  display: block;
  &:after,
  &:before{
      position: absolute;
      content: '';
      background-repeat: no-repeat;
      background-size: contain;
  }
  &::after{
      background-image: get-icon('wpg-card-bottom-bg');
      width: 73px;
      height: 83px;
      bottom: 0;
      right: 0;
  }
  &::before{
      background-image: get-icon('wpg-card-top-bg');
      width: 237px;
      height: 66px;
      top: 0;
      left: 0;
  }
  .wp-title{
      color: $white;
      display: flex;
      align-items: center;
      margin-bottom: 35px;
      .number{
          font-size: 24px;
          line-height: 20px;
          margin-right: 8px;
      }
      .text{
          font-size: 12px;
          line-height: 15px;
          max-width: 87px;
      }
  }
  .slab-listing{
      .slab-list{
          display: flex;
          align-items: center;
          +.slab-list{
              margin-top: 9px;
          }
          &.active{
              .slab-bar{
                  background: rgba($color: $white, $alpha: 0.4);
              }
          }
          .slab-bar{
              border-radius: 3px;
              padding: 12px 16px;
              background: rgba($color: $white, $alpha: 0.1);
              min-width: 177px;
              font-size: 14px;
              line-height: 17px;
              margin-right: 16px;
          }
          .slab-label{
              font-size: 12px;
              line-height: 17px;
              position: relative;
              padding-left: 19px;
              &:after{
                  position: absolute;
                  content: '';
                  width: 0; 
                  height: 0; 
                  top: 50%;
                  left: 0;
                  transform: translateY(-50%);
                  border-top: 10px solid transparent;
                  border-bottom: 10px solid transparent;
                  border-right: 10px solid $white;
              }
          }
      }
  }
  &.light-blue{
      background: $wpg-light-blue;
      color: $white;
  }
}

.wpg{
    .primary-card{
        max-width: none;
        .wpg-text{
            display: flex;
            align-items: center;
            margin-bottom: 0;
            .primary-title{
                margin-bottom: 0;
            }
            .wpg-name{
                margin-left: 12px;
                font-size: 12px;
            }
        }
    }
}