$circle-size: 48px;
$background-color: #f0f0f0; 
$progress-color: #4b86db;
$percent: 75;
$duration: 2s;

.progress-bar {
	position: relative;
	height: $circle-size;
	width: $circle-size;
	background-color: transparent;
	.center-content{
		position: absolute;
    z-index: 9;
    color: #000;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
		font-size: 12px;
		font-weight: 600;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	div {
		position: absolute;
		height: $circle-size;
		width: $circle-size;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
			span {
				line-height: $circle-size / 4 * 3;
				height: 46px;
				width: 46px;
				text-align: center;
				border-radius: 50%;
				color: $black;
				background-color: white;
			}
			.counter{
				width: auto;
				height: auto;
				line-height: 1.2;
			}
	}

	.background {
		background-color: $background-color;
	}
	.rotate {
		clip: rect(0 ($circle-size / 2) $circle-size 0);
		background-color: $progress-color;
		// animation: rotate $duration linear;
		// transform: rotate(-100deg);
		// transition: all (2s * $percent / 100) linear;
	}
	.left {
		clip: rect(0 ($circle-size / 2) $circle-size 0);
		opacity: 1;
		// animation: toggle ($duration / 2)  step-start;
		background-color: $background-color;
	}
	.right {
		clip: rect(0 ($circle-size / 2) $circle-size 0);
		transform: rotate(180deg);
		opacity: 0;
		// animation: toggle ($duration / 2) step-end;
		background-color: $progress-color;
	}
}

// @keyframes rotate {
// 	0%		{ transform: rotate(( -1 * $percent * 3.6) deg); }
// }

@keyframes toggle {
	0%		{ opacity: 0; }
	100%	{ opacity: 1; }
}