.team-heading{
  font-size: 20px;
  line-height: 28px;
  font-weight: 400;
  color: rgba($color: $white, $alpha: 0.7);
  span{
    color: rgba($color: $white, $alpha: 0.9);
  }
}
.team-search{
  .bottom-section{
    .recent-team-list{
      max-height: none;
      overflow-x: initial;
      border-bottom: none;
    }
    .team-with-icon{
      border-bottom: 1px solid rgba(0,0,0,.1);
      border-top: 1px solid rgba(0,0,0,.1);
      padding: 32px 20px;
      margin: 0 -20px 32px;
    }
  }
}
@keyframes opacityShow{
  0% {opacity: 1;}
  50% {opacity: 1;}
  100% {opacity: 0;}
}
.team-feedback{
  position: relative;
  .feedback-success{
    padding: 18px 22px;
    background: #288EEF;
    box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
    border-radius: 4px;
    display: flex;
    align-items: center;
    position: absolute;
    max-width: 320px;
    width: 100%;
    top: 16px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 9999;
    display: none;
    opacity: 0;
    transition: all 0.5s;
    &.active{
      display: flex;
      animation: opacityShow 5s;
    }
    img{
      width: 32px;
      height: 32px;
      margin-right: 12px;
    }
    p{
      font-weight: 500;
      font-size: 14px;
      line-height: 22px;
      color: #fff;
      margin-bottom: 0;
    }
  }
  .small-title{
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    margin-bottom: 16px;
  }
  .feedback{
    .chips-radio{
      overflow-x: auto;
    }
    .main-tab-body{
      position: relative;
      &:after{
        position: absolute;
        content: '';
        background: #e1e9ee;
        height: 2px;
        left: 0;
        right: 0;
        bottom: 0;
      }
    }
    .main-tabs{
      overflow-x: auto;
      &::after{
        display: none;
      }
      li{
        a{
          white-space: nowrap;
        }
      }
    }
    .tab-content{
      .circular-progress-body{
        display: flex;
        align-items: center;
        justify-content: space-between;
        .circular-chart{
          display: flex;
          flex-direction: column;
          align-items: center;
          .circular-bottom-label{
            margin-top: 8px;
            text-align: center;
            font-size: 12px;
            line-height: 20px;
          }
        }
      }
    }
  }
  .payout-card{
    border-radius: 4px;
    padding: 16px;
    background: rgba(88, 175, 255, 0.4);
    margin-top: 32px;
    .pay-text{
      font-size: 16px;
      line-height: 20px;
      color: #005EB5;
    }
    .pay-subtext{
      font-size: 12px;
      line-height: 20px;
      margin-top: 8px;
      color: rgba(0, 94, 181, 0.6);
    }
  }
}
.infra-accordion{
  .ta-accordion{
    .ta-card{
      +.ta-card{
        margin-top: 16px;
      }
      &.red{
        .select-list{
          li{
            &.active{
              background: #F89491;
            }
          }
        }
      }
      &.yellow{
        .select-list{
          li{
            &.active{
              background: #D7AC74;
            }
          }
        }
      }
      &.green{
        .select-list{
          li{
            &.active{
              background: rgba(13, 144, 57, 0.5);
            }
          }
        }
      }
      .collapse-bottom-detail{
        flex-direction: column;
        margin-top: 8px;
        border-top: 1px solid rgba(241, 56, 56, 0.1);
      }
      .select-block{
        width: 100%;
        +.select-block{
          margin-top: 16px;
        }
        .select-heading{
          display: flex;
          justify-content: space-between;
          align-items: center;
          p{
            font-size: 14px;
          }
        }
        .select-list{
          margin-top: 10px;
          display: flex;
          justify-content: space-between;
          li{
            background: rgba($color: $white, $alpha: 0.8);
            border-radius: 4px;
            width: 100%;
            height: 27px;
            +li{
              margin-left: 4px;
            }
          }
        }
      }
    }
  }
  .ta-heading{
    margin-bottom: 0;
    p{
      font-weight: 600;
      font-size: 16px;
    }
  }
}
.feed-heading{
  font-weight: 600;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 16px;
}
.performance{
  .perform-chart-body{
    margin-top: 32px;
  }
}
.achievement{
  $achievement-size: 72px;
  .circular-progress-body{
    &.with-scroll{
      overflow-x: auto;
      flex-wrap: nowrap;
    }
    .circular-chart{
      +.circular-chart{
        margin-left: 32px;
      }
    }
    .circular-bottom-label{
      font-size: 12px;
      color: rgba($color: $black, $alpha: 0.6);
    }
    .progress-bar{
      width: $achievement-size;
      height: $achievement-size;
      .center-content{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
      .price{
        font-size: 14px;
        font-weight: 700;
        color: $black;
        height: initial;
        width: initial;
        line-height: 18px;
      }
      .counter{
        font-size: 12px;
        color: rgba($color: $black, $alpha: 0.6);
        height: initial;
        width: initial;
        line-height: 18px;
      }
      div{
        width: $achievement-size;
        height: $achievement-size;
        span{
          line-height: $achievement-size / 4 * 3;
          height: 69px;
          width: 69px;
        }
      }
      .rotate,
      .left,
      .right{
        clip: rect(0 ($achievement-size / 2) $achievement-size 0);
      }
    }
  }
}
.execution{
  .chips-radio{
    .radio-label{
      width: auto;
    }
  }
}
.pie-chart{
  .pie-filter{
    display: flex;
    align-items: center;
    margin-bottom: 32px;
    span{
      font-weight: 500;
      font-size: 16px;
      line-height: 18px;
      color: rgba($color: $black, $alpha: 0.4);
      cursor: pointer;
      +span{
        margin-left: 24px;
      }
      &.active{
        color: $black;
        font-weight: 700;
      }
    }
  }
}
.pie-chart-legend{
  display: flex;
  flex-wrap: wrap;
  &.channel{
    .chart-legend{
      &:nth-child(1){
        &:after{
          background: #108CFE;
        }
      }
      &:nth-child(2){
        &:after{
          background: #58afff;
        }
      }
      &:nth-child(3){
        &:after{
          background: #86c5ff;
        }
      }
      &:nth-child(4){
        &:after{
          background: #dbc0ff;
        }
      }
      &:nth-child(5){
        &:after{
          background: #e8d7ff;
        }
      }
      &:nth-child(6){
        &:after{
          background: #f0e3ff;
        }
      }
      &:nth-child(7){
        &:after{
          background: #f7f1ff;
        }
      }
    }
  }
  &.category{
    .chart-legend{
      &:nth-child(1){
        &:after{
          background: #3AA0FE;
        }
      }
      &:nth-child(2){
        &:after{
          background: rgba(58, 160, 254, 0.5);
        }
      }
      &:nth-child(3){
        &:after{
          background: rgba(58, 160, 254, 0.2);
        }
      }
      &:nth-child(4){
        &:after{
          background: rgba(58, 160, 254, 0.1);
        }
      }
      &:nth-child(5){
        &:after{
          background: rgba(58, 160, 254, 0.05);
        }
      }
    }
  }
  .chart-legend{
    margin-bottom: 32px;
    padding-left: 12px;
    position: relative;
    width: 100%;
    max-width: 33.3333%;
    &:after{
      content: '';
      position: absolute;
      width: 4px;
      border-radius: 10px;
      top: 0;
      bottom: 0;
      left: 0;
    }
    .legend-name{
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 4px;
      color: rgba($color: $black, $alpha: 0.9);
    }
    .legend-count{
      font-size: 12px;
      line-height: 20px;
      color: rgba($color: $black, $alpha: 0.6);
      span{
        color: $black;
      }
    }
  }
}
.my-team{
  &.empty{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 73px);
    img{
      width: 96px;
      height: 96px;
      margin-bottom: 24px;
    }
    .empty-message{
      font-size: 14px;
      line-height: 18px;
      color: #2E2E2E;
      margin-bottom: 35px;
      max-width: 245px;
      text-align: center;
    }
    .custom-primary-button{
      width: 100%;
    }
  }
}