.notification-tab-section{
  .ova-tab-view{
    .nav{
      justify-content: center;
      margin-bottom: 0;
      li{
        position: relative;
        &.new-notification{
          padding-right: 12px;
          &:after{
            position: absolute;
            content: '';
            background: get-icon('dot', #1AC887);
            width: 8px;
            height: 8px;
            top: 0;
            right: 0;
          }
        }
      }
    }
  }
  .day-label{
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba($color: $black, $alpha: 0.6);
    margin-bottom: 16px;
    margin-top: 24px;
  }
  .update-flex{
    display: flex;
    align-items: flex-start;
    padding: 16px;
    margin: 0 -20px;
    border-bottom: 1px solid rgba($color: $black, $alpha: 0.1);
    img{
      width: 32px;
      height: 32px;
      margin-top: 5px;
    }
    .content-body{
      padding-left: 16px;
      width: 100%;
      .content{
        font-size: 14px;
        line-height: 22px;
        font-weight: 400;
      }
    }
    .update-action{
      margin-top: 8px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .update-time{
        font-size: 12px;
        line-height: 15px;
        color: rgba($color: $black, $alpha: 0.4);
      }
    }
    &.new-update{
      animation: newNotification 3s;
    }
  }
  .announcement-card{
    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    +.announcement-card{
      margin-top: 16px;
    }
    img{
      height: 210px;
      width: 100%;
      object-fit: cover;
    }
    .announcement-title{
      font-size: 14px;
      line-height: 17px;
      color: $second-body-color;
      margin-top: 16px;
      padding: 0 16px;
    }
    .announcement-desc{
      font-size: 14px;
      line-height: 22px;
      color: $second-body-color;
      margin-top: 8px;
      padding: 0 16px;
    }
    .announcement-time{
      font-size: 12px;
      line-height: 15px;
      text-align: right;
      color: rgba($color: $black, $alpha: 0.4);
      margin-top: 16px;
      padding: 0 16px 16px;
    }
  }
}
@keyframes newNotification{
  from {background-color: #f5f5f5;}
  to {background-color: #fff;}
}