.ova{
  .ova-tab-view{
    .main-tabs{
      margin-left: -20px;
      margin-right: -20px;
      margin-bottom: 20px;
      padding-left: 20px;
      padding-right: 20px;
      position: relative;
      display: flex;
      flex-wrap: nowrap;
      overflow-x: scroll;
      &:after{
        position: absolute;
        content: '';
        background: #E1E9EE;
        height: 2px;
        left: 0;
        right: 0;
        bottom: 0;
      }
      li{
        a{
          font-size: 14px;
          font-weight: 400;
          color: #2e2e2e;
          padding-bottom: 5px;
          position: relative;
          &.active{
            font-weight: 700;
            color: #2461FF;
            &:after{
              position: absolute;
              content: '';
              background: #2461FF;
              height: 2px;
              left: 0;
              right: 0;
              bottom: 0;
              border-radius: 4px;
              z-index: 1;
            }
          }
        }
      }
    }
  }
  .ova-dashboard{
    padding: 16px;
    border-radius: 4px;
    background: $green-card-footer;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    &.ova-main{
      &.yellow{
        background: $yellow-card-footer;
      }
      &.blue{
        background: $blue-card-footer;
      }
      &.red{
        background: $red-card-footer;
      }
      &.green{
        background: $green-card-footer;
      }
    }
    &.yellow{
      background-color: $yellow-card-bg;
      .tail-arrow{
        &.up,
        &.down{
          background-image: get-icon('arrow-tail', $arrow-yellow)
        }
      }
      .second-track{
        background: rgba($color: $yellow-card-text, $alpha: 0.3);
      }
      .third-track{
        background: $yellow-card-text;
      }
    }
    &.green{
      background-color: $green-card-bg;
      .tail-arrow{
        &.up,
        &.down{
          background-image: get-icon('arrow-tail', $arrow-green)
        }
      }
      .second-track{
        background: rgba($color: $green-card-text, $alpha: 0.3);
      }
      .third-track{
        background: $green-card-text;
      }
    }
    &.red{
      background-color: $red-card-bg;
      .tail-arrow{
        &.up,
        &.down{
          background-image: get-icon('arrow-tail', $arrow-red)
        }
      }
      .second-track{
        background: rgba($color: $red-card-text, $alpha: 0.3);
      }
      .third-track{
        background: $red-card-text;
      }
    }
    &.blue{
      background-color: $blue-card-bg;
      .tail-arrow{
        &.up,
        &.down{
          background-image: get-icon('arrow-tail', $arrow-blue)
        }
      }
      .second-track{
        background: rgba($color: $blue-card-text, $alpha: 0.3);
      }
      .third-track{
        background: $blue-card-text;
      }
    }
    &.ta-dashboard{
      flex-direction: column;
      align-items: initial;
      .green{
        .secondary-text{
          color: rgba($color: $white, $alpha: 0.8);
        }
        .second-track{
          background: $green-card-text;
        }
        .third-track{
          background: $dark-green;
        }
      }
      .bottom{
        margin-top: 24px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
      }
    }
    .ova-count{
      text-align: center;
      .ova-count-number{
        font-size: 14px;
        line-height: 17px;
        // color: $white;
        display: flex;
        align-items: center;
        span{
          +span{
            margin-left: 3px;
          }
        }
        .secondary{
          color: rgba($color: $white, $alpha: 0.8);
        }
      }
      .ova-count-label{
        margin-top: 6px;
        // color: rgba($color: $white, $alpha: 0.8);
        font-size: 12px;
        line-height: 15px;
        &.white{
          color: rgba($color: $white, $alpha: 0.8);
        }
      }
    }
  }
  .chips-radio{
    justify-content: start;
    .radio-label{
      max-width: none;
    }
  }
  .ta-track{
    .third-track{
      position: absolute;
      right: initial;
      left: 0;
      top: 0;
      border-radius: 8px;
      height: 100%;
    }
  }
}