.ta-card{
  border-radius: 4px;
  padding: 16px;
  &.green{
    background: $green-card-bg;
    .primary-text{
      color: $green-card-text;
    }
    .secondary-text{
      color: rgba($color: $green-card-text, $alpha: 0.6);
    }
    .second-track{
      background: rgba($color: $green-card-text, $alpha: 0.3);
    }
    .third-track{
      background: $green-card-text;
    }
    .ta-card-footer{
      background-color: $green-card-footer;
    }
  }
  &.red{
    background: $red-card-bg;
    .primary-text{
      color: $red-card-text;
    }
    .secondary-text{
      color: rgba($color: $red-card-text, $alpha: 0.6);
    }
    .second-track{
      background: rgba($color: $red-card-text, $alpha: 0.5);
    }
    .third-track{
      background: $red-card-text;
    }
    .ta-card-footer{
      background-color: $red-card-footer;
    }
  }
  &.yellow{
    background: $yellow-card-bg;
    .primary-text{
      color: $yellow-card-text;
    }
    .secondary-text{
      color: rgba($color: $yellow-card-text, $alpha: 0.6);
    }
    .second-track{
      background: rgba($color: $yellow-card-text, $alpha: 0.3);
    }
    .third-track{
      background: $yellow-card-text;
    }
    .ta-card-footer{
      background-color: $yellow-card-footer;
    }
  }
  .drill-footer{
    margin-top: 24px;
  }
}
.ta-card-main{
  padding: 0;
  +.ta-card-main{
    margin-top: 16px;
  }
  .ta-card-body{
    padding: 16px 16px 24px;
  }
}
.ta-card-content{
  +.ta-card-content{
    margin-top: 24px;
  }
}
.ta-card-footer{
  padding: 10px 16px;
  color: $white;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &.footer-top{
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-right-radius: 4px;
    border-top-left-radius: 4px;
  }
  p{
      text-overflow: ellipsis;
      overflow: hidden;
      .secondary-color{
        color: rgba($color: $white, $alpha: 0.8);
      }
  }
  .trending-label{
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    font-size: 12px;
    font-weight: 400;
  }
  .tail-arrow{
    width: 24px;
    height: 24px;
  }
}
.collapse-detail{
  cursor: pointer;
}
.ta-heading{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
  p{
    font-size: 14px;
  }
  .right{
    display: flex;
    align-items: center;
  }
  .drop-arrow{
    width: 16px;
    height: 16px;
    background-image: get-icon('drop-arrow', $second-body-color);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    margin-left: 12px;
  }
}
.ta-track{
  position: relative;
  background: $white;
  height: 8px;
  width: 100%;
  border-radius: 8px;
  .second-track{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    border-radius: 8px;
    .third-track{
      position: absolute;
      right: 0;
      top: 0;
      border-radius: 8px;
      height: 100%;
      left: initial;
    }
  }
}
.collapse-bottom-detail{
  padding-top: 24px;
  display: flex;
  justify-content: space-between;
  .collapse-detail-block{
    p{
      font-size: 16px;
      display: flex;
      align-items: center;
      justify-content: center;
      &.secondary-text{
        font-size: 12px;
        margin-top: 4px;
      }
      .tail-arrow{
        margin-right: 4px;
      }
    }
  }
}
.drop-arrow{
  transition: all 0.3s;
}
.collapse-detail[aria-expanded="true"]{
  .drop-arrow{
    transform: rotate(180deg);
  }
}
.ta-accordion{
  margin-top: 8px;
  &.shikhar{
    .collapse-bottom-detail{
      flex-wrap: wrap;
      justify-content: start;
      .collapse-detail-block{
        width: 33.33%;
        margin-bottom: 28px;
        p{
          justify-content: start;
        }
      }
    }
  }
  .ta-card{
    +.ta-card{
      margin-top: 8px;
    }
  }
}