.section-title{
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    img{
        width: 32px;
        height: 32px;
        margin-left: 5px;
    }
    .small-section-title{
        font-size: 14px;
        line-height: 1.2;
        font-weight: 400;
        margin-left: 4px;
    }
}
.heading-with-drop{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
    .section-title{
        margin-bottom: 0;
    }
}
.view-all{
    display: flex;
    align-items: center;
    position: relative;
    font-size: 14px;
    line-height: 22px;
    padding-right: 16px;
    color: $blue;
    &::after{
        position: absolute;
        content: '';
        right: 0;
        width: 12px;
        height: 12px;
        background: get-icon('right-icon', $blue);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
    }
}
.arrow{
    width: 16px;
    height: 16px;
    background-position: center;
    background-repeat: no-repeat;
    &.up{
        &.red{
            background-image: get-icon('up-arrow', $arrow-red)
        }
        &.blue{
            background-image: get-icon('up-arrow', $blue)
        }
        &.green{
            background-image: get-icon('up-arrow', $arrow-green)
        }
        &.gray{
            background-image: get-icon('up-arrow', #666)
        }
    }
    &.down{
        &.red{
            background-image: get-icon('down-arrow', $arrow-red)
        }
        &.blue{
            background-image: get-icon('down-arrow', $blue)
        }
        &.green{
            background-image: get-icon('down-arrow', $arrow-green)
        }
    }
}
.back-arrow{
    width: 24px;
    height: 24px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: get-icon('back-icon', $black);
}
.tail-arrow{ 
    width: 16px;
    height: 16px;
    background-position: center;
    background-repeat: no-repeat;
    &.up,
    &.down,
    &.right{
        &.red{
            background-image: get-icon('arrow-tail', $arrow-red)
        }
        &.yellow{
            background-image: get-icon('arrow-tail', $arrow-yellow)
        }
        &.blue{
            background-image: get-icon('arrow-tail', $blue)
        }
        &.green{
            background-image: get-icon('arrow-tail', $arrow-green)
        }
        &.white{
            background-image: get-icon('arrow-tail', $white)
        }
    }
    &.up{
        transform: rotate(180deg);
    }
    &.right{
        transform: rotate(-90deg);
    }
}
.search-button-icon{
    width: 24px;
