//scroll card body
.scroll-card-container{
    margin-left: -20px;
    margin-right: -20px;
}
.scroll-card-body{
    display: flex;
    overflow-x: scroll;
    flex-wrap: nowrap;
    white-space: nowrap;
    position: relative;
    &::after{
        content: ".";
        display: block;
        clear: both;
        visibility: hidden;
        line-height: 0;
        height: 0;
    }
    >.primary-card:nth-child(1){
      margin-left: 20px;
    }
}

//flex card body
.flex-card-body{
    display: flex;
    flex-wrap: wrap;
    margin-left: -4px;
    margin-right: -4px;
    .primary-card-col{
        padding-left: 4px;
        padding-right: 4px;
        margin-bottom: 8px;
    }
    .primary-card{
        margin-right: 0;
        height: 100%;
        width: inherit;
        min-width: initial;
        max-width: none;
    }
}

//custom button
.custom-button{
    background-color: $white;
    color: $second-body-color;
    padding: 8px 12px;
    border-radius: 8px;
    border: 1px solid #e1e9ee;
    box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
    font-size: 12px;
    &:hover,
    &:focus,
    &:active{
        color: $second-body-color;
    }
}

//close button
.close-button{
    width: 24px;
    height: 24px;
    background-image: get-icon('circle-close', $second-body-color);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

//card text color
.green{
    .primary-text{
      color: $green-card-text;
    }
    .secondary-text{
      color: rgba($color: $green-card-text, $alpha: .6);
    }
  }
  .red{
    .primary-text{
      color: $red-card-text;
    }
    .secondary-text{
      color: rgba($color: $red-card-text, $alpha: .6);
    }
  }
  .yellow{
    .primary-text{
      color: $yellow-card-text;
    }
    .secondary-text{
      color: rgba($color: $yellow-card-text, $alpha: .6);
    }
  }

  //rating star
  .stars{
      form{
          display: flex;
          align-items: center;
          justify-content: center;
      }
  }
  .star-radio-body{
      +.star-radio-body{
          margin-left: 24px;
      }
  }
   
  input.star { display: none; }
   
  label.star {
    float: right;
    font-size: 24px;
    color: rgba($color: $black, $alpha: 0.6);
    
  }
   
  input.star:checked ~ label.star:before {
    content: '\f005';
    color: #f2a900;
  }
   
  input.star-5:checked ~ label.star:before {
    color: #5AC17D;
  }
   
  input.star-1:checked ~ label.star:before { color: #FE6767; }
   
  label.star:before {
    content: '\f006';
    font-family: FontAwesome;
  }