#comment{
  .feed-details{
    margin: 0;
    padding-top: 0;
    .feed-detail-card{
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      box-shadow: none;
      padding-left: 0;
      padding-right: 0;
      border-radius: 0;
      +.feed-detail-card{
        margin-top: 0;
      }
      .fd-bottom{
        border-top: 0;
        padding-top: 0;
        .view-comment-link{
        }
        .fd-action-button{
          margin: 24px 0;
          .like-counter{
            padding-right: 10px;
          }
        }
      }
      &.main-comment{
        padding: 0;
      }
      &.sub-comment{
        .feed-post-text{
          padding-left: 41px;
          margin-top: 0;
          margin-bottom: 8px;
        }
        .fd-bottom{
          padding-left: 41px;
          .fd-action-button{
            margin: 0;
            justify-content: flex-start;
            .like-number{
              margin-right: 8px;
              font-size: 12px;
              line-height: 20px;
              color: #494949;
            }
          }
        }
      }
    }
  }
}