.filter-bar{
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    margin-right: -20px;
    margin-left: -20px;
    .filter-title{
        font-size: 14px;
        line-height: 18px;
        color: #2e2e2e;
        padding-left: 20px;
        margin-right: 12px;
        white-space: nowrap;
    }
}
//chips-radio
.chips-radio{
  display: flex;
  flex-wrap: nowrap;
  align-items: stretch;
  justify-content: space-between;
  margin-left: -4px;
  margin-right: -4px;
  .radio-label{
      display: block;
      position: relative;
      width: 100%;
      max-width: 25%;
      padding-left: 4px;
      padding-right: 4px;
      input[type="radio"]:checked ~ .radio-text{
          border-color: $blue;
          color: $blue;
      }
      input[type="checkbox"]:checked ~ .radio-text{
        border-color: $blue;
        color: $blue;
    }
      .radio-text{
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 8px 10px;
          border: 1px solid #e1e9ee;
          border-radius: 8px;
          font-size: 12px;
          color: $second-body-color;
          white-space: nowrap;
          span{
              +span{
                  margin-left: 5px;
              }
          }
      }
  }
}