.select-team-body .search-bar input[type="text"] {
  font-size: 14px;
  line-height: 18px;
  border: none;
}

.search-bar {
  position: relative;
}

.search-bar input[type="text"] {
  border: 1px solid #ededed;
  box-shadow: none;
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-size: 12px;
  line-height: 18px;
  padding: 4px 28px 4px 14px;
  background: transparent;
}

.search-bar .close-icon {
  position: absolute;
  right: 14px;
  top: 50%;
  transform: translateY(-50%);
  width: 12px;
  height: 12px;
  cursor: pointer;
}

.search-bar.with-searchbar {
  display: none;
  margin-bottom: 16px;
}

.search-label {
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 0.5px;
  margin-bottom: 16px;
