.chips-navigation {
  position: sticky;
  top: 71px;
  z-index: 99;
  background-color: #fff;
}

.chips-navigation .navigation-listing {
  position: relative;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  overflow: auto hidden;
}

.chips-navigation .navigation-listing:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.chips-navigation .navigation-listing .navigation-list {
  margin-right: 10px;
}

.chips-navigation .navigation-listing .navigation-list:first-child {
  padding-left: 20px;
}

.chips-navigation .navigation-listing .navigation-list.active .navigation-link {
  background-color: #2461FF;
  border-color: #2461FF;
  color: #fff;
}

.chips-navigation .navigation-listing .navigation-list .navigation-link {
  padding: 12px;
  border-radius: 8px;
  border: 1px solid #E1E9EE;
  color: #252E5D;
  display: block;
  white-space: nowrap;
}

.filter-bar {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  margin-right: -20px;
  margin-left: -20px;
}

.filter-bar .filter-title {
  font-size: 14px;
  line-height: 18px;
  color: #2e2e2e;
  padding-left: 20px;
  margin-right: 12px;
  white-space: nowrap;
}

.chips-radio {
  display: flex;
  flex-wrap: nowrap;
  align-items: stretch;
  justify-content: space-between;
  margin-left: -4px;
  margin-right: -4px;
}

.chips-radio .radio-label {
  display: block;
  position: relative;
  width: 100%;
  max-width: 25%;
  padding-left: 4px;
  padding-right: 4px;
}

.chips-radio .radio-label input[type="radio"]:checked ~ .radio-text {
  border-color: #2461FF;
  color: #2461FF;
}

.chips-radio .radio-label input[type="checkbox"]:checked ~ .radio-text {
  border-color: #2461FF;
  color: #2461FF;
}

.chips-radio .radio-label .radio-text {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 10px;
