.setting .push-notification {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  border-radius: 8px;
  padding: 20px 16px;
  box-shadow: 0px 2px 6px rgba(84, 105, 119, 0.101961);
}

.setting .push-notification p {
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #000;
}

.setting .push-notification .switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 25px;
}

.setting .push-notification .switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.setting .push-notification .switch .slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

.setting .push-notification .switch .slider.round {
  border-radius: 34px;
}

.setting .push-notification .switch .slider.round:before {
  border-radius: 50%;
}

.setting .push-notification .switch input:checked + .slider {
  background-color: #2196F3;
}

.setting .push-notification .switch input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

.setting .push-notification .switch input:checked + .slider:before {
  -webkit-transform: translateX(15px);
  -ms-transform: translateX(15px);
  transform: translateX(15px);
}

