.kpi-empty{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  p{
    font-size: 18px;
    line-height: 22px;
    color: $second-body-color;
    font-weight: 400;
    max-width: 290px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
}
.kpi-no-found{
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  p{
    font-size: 14px;
    line-height: 18px;
    color: #2e2e2e;
    max-width: 245px;
    text-align: center;
    margin: 26px auto 0;
  }
  img{
    width: 92px;
    height: 92px;
  }
}
.feed-empty{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 70px;
  img{
    width: 72px;
    height: 72px;
    margin-bottom: 36px;
  }
  p{
    text-align: center;
    max-width: 245px;
    font-size: 14px;
    line-height: 18px;
    color: #2e2e2e;
  }
}