//chips-navigation
.chips-navigation{
  position: sticky;
  top: 71px;
  z-index: 99;
  background-color: $white;
  .navigation-listing{
      position: relative;
      display: flex;
      align-items: center;
      flex-wrap: nowrap;
      overflow: auto hidden;
      &:after{
        content: ".";
        display: block;
        clear: both;
        visibility: hidden;
        line-height: 0;
        height: 0;
      }
      .navigation-list{
          margin-right: 10px;
          &:first-child{
              padding-left: 20px;
          }
          &.active{
              .navigation-link{
                  background-color: $blue;
                  border-color: $blue;
                  color: $white;
              }
          }
          .navigation-link{
              padding: 12px;
              border-radius: 8px;
              border: 1px solid #E1E9EE;
              color: $second-body-color;
              display: block;
              white-space: nowrap;
          }
      }
  }
}
