@import "../../../node_modules/reset-css/reset";
@import "./typography";

body{
    overflow-x: hidden;
    font-family: 'Inter';
    font-size: 14px;
    font-weight: 400;
    color: $black;
    background: $black;
    &.no-scroll{
        overflow-y: hidden;
    }
    .body-wrapper{
        width: 100%;
        min-height: 100vh;
        height:100%;
        max-width: 375px;
        margin: 0 auto;
        padding-bottom: 56px;
        background: #fafafa;
        &.white-bg{
            background: #fff;
        }
        &.team-bg{
            background: #0F5598;
        }
        @include tablet(){
            max-width: 768px;
        }
        @include mobile(){
            max-width: 480px;
        }
        &.no-footer{
            padding-bottom: 0;
        }
    }
}
a{
    text-decoration: none;
    &:hover{
        text-decoration: none;
    }
}

//custom scrollbar
/* width */
::-webkit-scrollbar {
    width: 2px;
    height: 3px;
    display: none;
  }
  
  /* Track */
::-webkit-scrollbar-track {
    box-shadow: transparent; 
    border-radius: 0;
}
   
  /* Handle */
::-webkit-scrollbar-thumb {
    background: rgba($color: $black, $alpha: .5); 
    border-radius: 10px;
}
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: $black; 
  }

//bootstrap select
.dropdown-toggle::after{
    border: none;
    border-top: none !important;
    border-left: none !important;
    border-bottom: none !important;
    border-right: none !important;
    // border: solid $second-body-color;
    // border-width: 0 1px 1px 0;
    display: inline-block;
    // padding: 3px;
    // transform: rotate(45deg);
    // -webkit-transform: rotate(45deg);
    // margin-top: -4px !important;
    margin-top: 0 !important;
    margin-left: 4px;
    width: 16px;
    height: 16px;
    background-image: get-icon('drop-arrow', $second-body-color);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
.dropdown{
    width: auto !important;
    &.bootstrap-select{
        .btn-light{
            outline: none !important;
            padding: 8px 12px;
            box-shadow: none !important;
            background-color: transparent;
            border: 1px solid #d3d3d3;
            border-radius: 8px;
            font-size: 12px;
            color: $second-body-color;
            width: auto;
            justify-content: inherit !important;
            &:hover{
                background-color: transparent;
            }
        }
        .filter-option{
            text-align: inherit;
            width: auto;
        }
    }
    .dropdown-menu{
        font-size: 12px;
        line-height: 22px;
        color: $second-body-color;
        border: 1px solid #e1e9ee;
        border-radius: 8px;
        .dropdown-item{
            color: $second-body-color;
            &.active{
                background-color: $prp-light-blue;
                color: $prp-text;
            }
        }
    }
}
//owl carousel
.owl-dots{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 8px;
    .owl-dot{
        width: 8px;
        height: 8px;
        border-radius: 50%;
        margin-right: 4px;
        background-color: #DBE5FF !important;
        &.active{
            background-color: #2461FF !important;
        }
    }
}
//hide radio
input[type="radio"],
input[type="checkbox"]{
    opacity: 0;
    display: none;
}

.tick-check-list{
    .check-label {
        display: block;
        position: relative;
        padding-left: 41px;
        cursor: pointer;
        font-size: 14px;
        line-height: 18px;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
      }
      
      /* Hide the browser's default checkbox */
      .check-label input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
      }
      
      /* Create a custom checkbox */
      .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        background: #FFFFFF;
        border: 1px solid rgba(36, 97, 255, 0.3);
        box-sizing: border-box;
        border-radius: 2px;
        height: 16px;
        width: 16px;
      }
      
      /* On mouse-over, add a grey background color */
      .check-label:hover input ~ .checkmark {
        background-color: $white;
      }
      
      /* When the checkbox is checked, add a blue background */
      .check-label input:checked ~ .checkmark {
        background-color: $blue;
      }
      
      /* Create the checkmark/indicator (hidden when not checked) */
      .checkmark:after {
        content: "";
        position: absolute;
        display: none;
      }
      
      /* Show the checkmark when checked */
      .check-label input:checked ~ .checkmark:after {
        display: block;
      }
      
      /* Style the checkmark/indicator */
      .check-label .checkmark:after {
        left: 4px;
        top: 3px;
        width: 8px;
        height: 8px;
        background: get-icon('tick');
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
}

[draggable] {
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    /* Required to make elements draggable in old WebKit */
    -khtml-user-drag: element;
    -webkit-user-drag: element;
  }

  ::placeholder{
      color: rgba($color: #2e2e2e, $alpha: 0.3);
  }