.bottom-message-bar{
  display: none;
  position: fixed;
  background-color: rgba($color: $black, $alpha: .5);
  z-index: 9999;
  max-width: 375px;
  width: 100%;
  top: 0;
  bottom: 0;
  &.active{
    display: block;
  }
  @include tablet(){
    max-width: 768px;
  }
  @include mobile(){
    max-width: 480px;
  }
  .message-bar-overlay{
    background-color: rgba($color: $black, $alpha: .5);
    width: 100%;
    top: 0;
    bottom: 0;
    position: absolute;
  }
  .message-bar-body{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: $white;
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
    padding: 12px 24px 24px;
    .close-bar-body{
      display: flex;
      justify-content: center;
      margin-bottom: 32px;
      .close-bar{
        width: 64px;
        height: 4px;
        border-radius: 4px;
        background: #e7ebef;
      }
    }
    .bottom-bar-links{
      display: flex;
      align-items: center;
      color: #2E2E2E;
      +.bottom-bar-links{
        margin-top: 20px;
      }
      span{
        +span{
          margin-left: 8px;
        }
      }
    }
  }
}
.rating-bar{
  .message-bar-body{
    padding-top: 48px;
  }
  .secondary-text{
    color: rgba($color: $black, $alpha: .6);
  }
  .rating-title{
    font-size: 14px;
    font-weight: 700;
    color: rgba($color: $black, $alpha: .9);
    margin-bottom: 8px;
    text-align: center;
  }
  .rating-subtitle{
    font-size: 14px;
    margin-bottom: 24px;
    text-align: center;
  }
  .star-body{
    display: flex;
    justify-content: center;
    span+span{
      margin-left: 24px;
    }
  }
  .close-icon{
    width: 12px;
    height: 12px;
    position: absolute;
    top: 24px;
    right: 20px;
  }
  &.slightly{
    .slightly-header{
      border-bottom: 1px solid rgba($color: $black, $alpha: .1);
      padding-bottom: 16px;
      margin-left: -24px;
      margin-right: -24px;
    }
    .rating-title{
      font-size: 18px;
      font-weight: 700;
      line-height: 24px;
      margin-bottom: 28px;
      color: rgba($color: $black, $alpha: .9);
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        width: 32px;
        height: 32px;
        margin-right: 8px;
      }
    }
    input[type="text"]{
      border: none;
      outline: none;
      box-shadow: none;
      border-bottom: 1px solid rgba($color: $black, $alpha: .1);
      padding-bottom: 16px;
      font-size: 14px;
      width: 100%;
    }
    .custom-primary-button{
      margin-top: 24px;
    }
    .rating-reason{
      margin: 16px -24px 30px;
      overflow-x: hidden;
      .reason-label{
        padding-left: 24px;
        margin-bottom: 16px;
        font-size: 14px;
        line-height: 18px;
        color: rgba($color: $black, $alpha: .6);
      }
      .reason-label-answer{
        font-size: 12px;
        line-height: 16px;
        color: rgba($color: $black, $alpha: .6);
        text-align: center;
        padding: 0 20px 24px;
      }
      .reason-label-suggestion{
        padding: 0 20px;
        text-align: center;
        font-size: 14px;
        line-height: 18px;
        color: rgba($color: $black, $alpha: .9);
      }
      .chips-radio{
        overflow-x: auto;
      }
      label{
        width: auto;
        max-width: unset;
        white-space: nowrap;
        &:nth-child(1){
          padding-left: 24px;
        }
        input[type=radio]:checked~.radio-text{
          font-weight: 500;
        }
        p{
          font-size: 14px;
        }
      }
    }
  }
  .map-review-card{
    .review-card-header{
      margin-bottom: 16px;
      .name-dot{
        display: flex;
        align-items: center;
        .review-card-name{
          margin-right: 8px;
          font-weight: 500;
          font-size: 14px;
          line-height: 18px;
        }
        .feedback-btn{
          font-weight: 600;
          font-size: 12px;
          line-height: 18px;
          color: #1678fb;
          margin-left: 8px;
        }
      }
    }
    .last-seen{
      font-size: 12px;
      line-height: 20px;
      color: rgba($color: $black, $alpha: .6);
      margin-bottom: 24px;
      span{
        font-weight: 500;
      }
    }
    .review-card-data-body{
      display: flex;
      justify-content: space-between;
      .review-data{
        width: 50%;
        p{
          font-size: 14px;
          line-height: 22px;
          font-weight: 700;
          span{
            font-weight: 400;
            color: rgba($color: $black, $alpha: .4);
          }
        }
      }
      +.review-card-data-body{
        margin-top: 20px;
      }
    }
  }
}
.streak{
  .streak-icon{
    display: flex;
    flex-direction: column;
    align-items: center;
    img{
      width: 88px;
      height: 88px;
      margin-bottom: 14px;
    }
    .streak-name{
      font-size: 14px;
      color: rgba($color: $black, $alpha: .6);
    }
  }
  .streak-text{
    max-width: 247px;
    margin: 32px auto 0;
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    color: rgba($color: $black, $alpha: .9);
  }
}