.rounded-menu{
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
  border: 1px solid #F3F3F3;
  border-radius: 50%;
  background: $white;
  &.blue{
    background: $blue;
    border: 1px solid $blue;
  }
}
.dot{
  display: block;
  width: 8px;
  height: 8px;
  margin-left: 8px;
  &.red{
      background: get-icon('dot', #FE6767);
  }
  &.green{
      background: get-icon('dot', #5AC17D);
  }
}
.floating-div{
  width: 100%;
  max-width: 375px;
  position: fixed;
  bottom: 16px;
  right: 0;
  left: 50%;
  padding-right: 16px;
  transform: translateX(-50%);
  display: flex;
  justify-content: flex-end;
  @include tablet(){
    max-width: 768px;
  }
  @include mobile(){
      max-width: 480px;
  }
}
#map{
  padding-top: 0;
  &.with-header{
    padding-top: 84px;
  }
  .review-l2{
    .map-review-card{
      background: transparent;
      box-shadow: none;
      padding: 0;
      .name-dot{
        display: flex;
        align-items: center;
      }
      .review-card-name{
        font-size: 20px;
        font-weight: 700;
        max-width: initial;
      }
      .gray-card{
        background: #F5F5F5;
        border-radius: 4px;
        padding: 16px;
        margin-top: 16px;
      }
      .review-chips-text{
        display: flex;
        align-items: center;
        justify-content: space-between;
        .impact{
          text-transform: uppercase;
          font-size: 10px;
          line-height: 12px;
          letter-spacing: 0.5px;
          color: rgba($color: $black, $alpha: 0.6);
        }
      }
      .review-card-footer{
        margin-top: 24px;
        .review-card-left{
          flex-direction: column;
          align-items: flex-start;
          .review-owner-name{
            margin-bottom: 8px;
            font-size: 14px;
          }
          .suggestion{
            font-size: 12px;
            margin-top: 16px;
            color: rgba($color: $black, $alpha: 0.6);
            display: flex;
            align-items: center;
            span{
              margin-right: 8px;
            }
          }
        }
      }
    }
  }
  .top-map{
    height: calc(100vh - 56px);
    &.relative{
      position: relative;
    }
    &.initial{
      height: calc(100vh - 340px);
    }
  }
  .bottom-feed{
    padding: 45px 32px 24px;
    transform: initial;
    top: initial;
    bottom: 56px;
    .close-bar-body{
      top: 20px;
      padding: 0;
    }
    .bottom-feed-header{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .maps-bottom-title{
      font-size: 14px;
      font-weight: 500;
      color: rgba($color: $black, $alpha: 0.9);
    }
    .search-categories-container{
      margin-top: 24px;
      .search-categories-button{
        border: 1px solid #D5E1FF;
        border-radius: 8px;
        padding: 12px 16px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        +.search-categories-button{
          margin-top: 16px;
        }
        p{
          font-size: 12px;
          line-height: 22px;
          color: rgba(0, 0, 0, 0.6);
          span{
            color: $black;
          }
        }
      }
    }
  }
  .filter-control-body{
    position: absolute;
    bottom: 72px;
    width: 100%;
    max-width: 375px;
      @include tablet(){
        max-width: 768px;
      }
      @include mobile(){
          max-width: 480px;
      }
    &.list-view{
      // position: fixed;
      bottom: initial;
      top: 82px;
      // background: $white;
      max-width: 375px;
      @include tablet(){
        max-width: 768px;
      }
      @include mobile(){
          max-width: 480px;
      }
      .map-filter-chips-container{
        margin-top: 0;
      }
    }
    &.sales{
      &.no-footer{
        bottom: 16px;
      }
      .map-filter-chips-container{
        margin-top: 0;
        ul{
          li{
            &:nth-child(1){
              padding-left: 0;
            }
          }
        }
      }
    }
    .filter-icon-body{
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding-left: 20px;
      padding-right: 20px;
      &.right-icon{
        justify-content: flex-end;
        &.list-only{
          .rounded-menu{
            width: 50px;
            height: 50px;
            .list-icon{
              width: 32px;
              height: 32px;
            }
          }
        }
      }
      .view-list-button{
        font-size: 12px;
        font-weight: 600;
        line-height: 22px;
        display: flex;
        align-items: center;
        color: $blue;
        background: $white;
        padding: 9px 16px;
        border-radius: 20px;
        span{
          margin-left: 4px;
        }
      }
    }
  }
  .map-filter-chips-container{
    margin-top: 16px;
    ul{
      display: flex;
      overflow-x: scroll;
      position: relative;
      &:after{
        content: ".";
        display: block;
        clear: both;
        visibility: hidden;
        line-height: 0;
        height: 0;
      }
      li{
        &:nth-child(1){
          padding-left: 20px;
        }
        +li{
          margin-left: 8px;
        }
        .map-filter-chips{
          font-size: 12px;
          line-height: 15px;
          font-weight: 400;
          border-radius: 20px;
          padding: 8px 16px;
          color: $black;
          background: $white;
          display: block;
          white-space: nowrap;
          &.blue{
            border: 1.5px solid #00518B;
          }
          &.green{
            border: 1.5px solid #5AC17D;
          }
          &.yellow{
            border: 1.5px solid #F2A900;
          }
          &.pink{
            border: 1.5px solid #FE6767;
          }
          &.active{
            font-weight: 500;
            color: $white;
            &.blue{
              background: #00518B;
            }
            &.green{
              background: #5AC17D;
            }
            &.yellow{
              background: #F2A900;
            }
            &.pink{
              background: #FE6767;
            }
          }
        }
      }
    }
  }
  .on-map-review{
    position: absolute;
    bottom: 72px;
    left: 50%;
    transform: translateX(-50%);
    right: 0;
    padding: 0 20px;
    max-width: 375px;
    width: 100%;
    @include tablet(){
      max-width: 768px;
    }
    @include mobile(){
        max-width: 480px;
    }
    &.sales{
      .review-card-header{
        margin-bottom: 8px;
        .name-dot{
          margin-bottom: 0;
          p{
            +p{
              margin-left: 8px;
            }
          }
        }
        .review-card-right{
          .track{
            font-weight: 600;
            font-size: 12px;
            line-height: 22px;
            color: $blue;
          }
        }
      }
      .last-seen{
        font-size: 12px;
        line-height: 18px;
        color: rgba($color: $black, $alpha: 0.6);
      }
      .review-card-data-body{
        +.review-card-data-body{
          margin-top: 16px;
        }
        .review-data{
          width: 50%;
          text-align: left;
          .review-count{
            span{
              font-size: 14px;
              font-weight: 400;
              color: rgba($color: $black, $alpha: 0.4);
            }
          }
        }
      }
    }
  }
  .map-review-card{
    background: $white;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.09);
    border-radius: 4px;
    padding: 12px 16px;
    display: block;
    +.map-review-card{
      margin-top: 8px;
    }
    .filled-star{
      display: block;
      width: 16px;
      height: 16px;
      margin-left: 8px;
      background: get-icon('filled-star');
    }
    .review-card-header{
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      .review-card-left{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }
      .name-dot{
        display: flex;
        align-items: center;
        margin-bottom: 8px;
        .dot{
          display: block;
          width: 8px;
          height: 8px;
          margin-left: 8px;
          &.red{
              background: get-icon('dot', #FE6767);
          }
          &.green{
              background: get-icon('dot', #5AC17D);
          }
        }
      }
      .review-card-name{
        font-size: 14px;
        line-height: 18px;
        font-weight: 500;
        color: $black;
        max-width: 130px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      .review-card-right{
        display: flex;
        align-items: center;
        .review-update-time{
          font-size: 10px;
          line-height: 2;
          font-style: italic;
          white-space: nowrap;
          text-align: right;
          color: rgba($color: $black, $alpha: 0.6);
          @include iphone-5(){
            word-break: break-word;
            white-space: normal;
            line-height: 1.2;
          }
        }
        .close-icon{
          margin-left: 12px;
          width: 12px;
          height: 12px;
        }
      }
    }
    .review-card-data-body{
      display: flex;
      align-items: flex-start;
      margin-top: 24px;
      .review-data{
        font-size: 14px;
        line-height: 18px;
        text-align: center;
        color: $black;
        +.review-data{
          margin-left: 24px;
        }
        .review-count-name{
          font-size: 12px;
          color: rgba($color: $black, $alpha: 0.6);
        }
      }
    }
    .review-card-footer{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 30px;
      .review-card-left,
      .review-card-right{
        display: flex;
        align-items: center;
      }
      .review-card-right{
        .direction{
          font-weight: 600;
          font-size: 12px;
          line-height: 22px;
          color: $blue;
        }
        .call-icon{
          background: get-icon('call-icon', $blue);
          margin-left: 8px;
        }
      }
      .review-owner-name{
        font-size: 12px;
        margin-right: 8px;
        color: $black;
        span{
          color: rgba($color: $black, $alpha: 0.6);
        }
      }
    }
  }
}
.review-card-chips{
  border: 1px solid #EDF0F5;
  border-radius: 12px;
  padding: 4px 8px;
  font-size: 11px;
  line-height: 14px;
  color: #6C718B;
  background: $white;
}
.review-card-list{
  margin-top: 48px;
  padding-bottom: 48px;
  &.sales{
    .map-review-card{
      +.map-review-card{
        margin-top: 16px !important;
      }
    }
    .review-card-header{
      margin-bottom: 8px;
      .name-dot{
        margin-bottom: 0;
        p{
          +p{
            margin-left: 8px;
          }
        }
      }
      .review-card-right{
        .track{
          font-weight: 600;
          font-size: 12px;
          line-height: 22px;
          color: $blue;
        }
      }
    }
    .last-seen{
      font-size: 12px;
      line-height: 18px;
      color: rgba($color: $black, $alpha: 0.6);
    }
    .review-card-data-body{
      +.review-card-data-body{
        margin-top: 16px;
      }
      .review-data{
        width: 50%;
        text-align: left !important;
        .review-count{
          span{
            font-size: 14px;
            font-weight: 400;
            color: rgba($color: $black, $alpha: 0.4);
          }
        }
      }
    }
  }
}
.view-map-button{
  padding: 7px 12px;
  border-radius: 4px !important;
  background: $blue;
  color: $white;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  display: inline-block;
  width: initial !important;
  height: initial !important;
  &:hover{
    color: $white;
  }
}
.view-map-icon{
  width: 50px;
  height: 50px;
  background: $blue;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  .maps-icon{
    width: 32px;
    height: 32px;
  }
}
.map-feedback{
  .feedback-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-bottom: 20px;
    h2{
      font-weight: 600;
      font-size: 20px;
      color: rgba($color: $black, $alpha: 0.9);
    }
    .action{
      display: flex;
      align-items: center;
      .action-button{
        font-weight: 600;
        font-size: 12px;
        color: #1678FB;
        margin-right: 8px;
      }
    }
  }
  .feedback-card{
    background: $white;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.04);
    border-radius: 4px;
    padding: 16px;
    .fcard-header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 16px;
      .f-tag{
        font-size: 10px;
        line-height: 12px;
        color: rgba($color: $black, $alpha: 0.6);
      }
      .f-time{
        font-size: 12px;
        color: rgba($color: $black, $alpha: 0.4);
      }
    }
    .fcard-body{
      display: flex;
      align-items: center;
      margin-bottom: 16px;
      .rating-number{
        display: flex;
        align-items: center;
        span{
          margin-right: 4px;
        }
      }
      .f-text{
        margin-left: 20px;
        font-weight: 500;
        font-size: 14px;
      }
    }
    .fcard-footer{
      font-size: 12px;
      color: rgba($color: $black, $alpha: 0.6);
    }
  }
}
.review-service-list{
  padding: 0 20px;
  li{
    padding: 16px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    +li{
      margin-top: 16px;
    }
    p{
      font-size: 16px;
    }
    &.red{
      background: $red-card-bg;
      p{
        color: $red-card-text;
      }
    }
    &.yellow{
      background: $yellow-card-bg;
      p{
        color: $yellow-card-text;
      }
    }
    &.green{
      background: $green-card-bg;
      p{
        color: $green-card-text;
      }
    }
  }
}
.channel-card{
  padding: 0 20px;
  $channel-circular-width: 80px;
  .channel-card-body{
    border-radius: 4px;
    padding: 20px;
    background: $green-card-bg;
    display: flex;
    align-items: center;
    .channel-circular-progress{
      .position{
        height: $channel-circular-width;
	      width: $channel-circular-width;
        .background{
          background: #fff;
        }
        div{
          height: $channel-circular-width;
	        width: $channel-circular-width;
          span{
            line-height: $channel-circular-width / 4 * 3;
            width: 76px;
            height: 76px;
            background: $green-card-bg;
          }
        }
        .rotate,
        .left,
        .right{
          clip: rect(0 ($channel-circular-width / 2) $channel-circular-width 0);
        }
        .center-content{
          display: none;
        }
        .inside-circle{
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          z-index: 99;
          display: flex;
          flex-direction: column;
          align-items: center;
          .bold{
            font-size: 16px;
            color: $black;
          }
          .light-gray{
            font-size: 12px;
            color: rgba($color: $black, $alpha: 0.6);
            margin-top: 4px;
          }
        }
      }
    }
  }
  .channel-text{
    border-left: 2px solid #5AC17D;
    padding-left: 10px;
    margin-left: 32px;
    .channel-number{
      font-size: 14px;
      line-height: 18px;
      color: $black;
    }
    .channel-desc{
      font-size: 12px;
      line-height: 18px;
      color: rgba($color: $black, $alpha: 0.6);
    }
  }
}
.bottom-section{
  &.map{
    &.ova{
      .ova-tab-view{
        .main-tabs{
          margin-left: 0;
          margin-right: 0;
          justify-content: space-between;
          flex-wrap: nowrap;
          li{
            width: 33.33%;
            a{
              justify-content: center;
            }
          }
        }
      }
    }
  }
}